<template>
<div class="my-popups">
    <div class="modal d-block ">
        <div class="modal-dialog modal-dialog-centered ui-draggable">
            <div class="modal-content">
                <div class="modal-header ui-draggable-handle">
                    <h4 name="header" class="modal-title">
                        {{manageFolderPopupLabel}}
                    </h4>
                    <button @click="closemanagepopup" class="close"><em aria-hidden="true" class="fa fa-times"></em> </button>
                </div>
                <div class="modal-body">
                    <section id="divMainContent" class="main-content">
                                <div class="row">
                                    <div class="col-lg-4">
                                            <div class="form-group">
                                                <label>Parent Folder:</label>
                                                <div class="form-control alert-success">
                                                   
                                                    <a id="aChooseParent"  @click="OpenPopup"  v-bind:folderid="folderId" href="javascript:;" data-toggle="modal"  data-target="#parentfolder" class="text-success folderpath">{{commonParentName}} > {{breadcrumbForManagePopup}}</a>
                                                </div>
                                            </div>
                                    </div>
                                    <div class="col-lg-4" v-if="docCode==0">
                                            <div class="form-group">
                                                <label>Folder Name:</label><span class="text-danger">*</span>
                                                <input @keydown.enter="SaveFolder(folderIdmanage)" class="form-control" id="FolderName" maxlength="20" v-model.trim="folderName" type="text"/>
                                                <span class="text-danger" Style="font-size: 80%;">{{errorMessage}}</span>
                                            </div>
                                    </div>
                                    <div v-if="docCode==1" class="col-lg-4">
                                        <div class="form-group">
                                            <label>File Name:</label><span class="text-danger">*</span>
                                            <input @keydown.enter="SaveFile(fileId,folderId,fileName)" id="fileName" class="form-control" v-bind:folderid="folderId" v-bind:fileId="fileId"  v-model.trim="fileName" type="text"/>
                                            <span class="text-danger" Style="font-size: 80%;">{{ errorMessage }}</span>
                                        </div>
                                    </div>
                                </div>
                    </section>
                </div>
                <div class="modal-footer text-right">
                    <button title="Search"  v-if="docCode==0" @click.prevent="SaveFolder(folderIdmanage)" id="AddFolder" class="popupgreenbtn btn btn-success formbtn widthhalf pr-2"><em class="fa fa-floppy-o pr-2"></em> Save</button> 
                    <button title="Search" @keydown.enter="SaveFile(fileId,folderId,fileName)" v-if="docCode==1" @click="SaveFile(fileId,folderId,fileName)" class="popupgreenbtn btn btn-success formbtn widthhalf pr-2"><em class="fa fa-floppy-o pr-2"></em> Save</button> 
                    <button title="Cancel"  @click="closemanagepopup" class="popupredbtn btn btn-danger formbtn widthhalf pr-2"><em aria-hidden="true" class="fa fa-times pr-2"></em> Cancel</button>
                </div>
            </div>
        </div>
    </div>
</div>
</template>
<script>
import Ownertree from "@/components/OwnerTree.vue"
import DataService from "../../services/DataService"
export default {
    components:{
        Ownertree,
    },
    data(){
        return {
                loggeduserid:null,
                companyId:null,
                showmodal: false,
                foldername:null,
                folderIdmanage:null,
                modalLabel:"New Folder",
                errorMessage:""
            }
    },
    created: function () {
        
        var vm = this;
        vm.loggeduserid=this.GetUserInfo.ID;
        vm.companyId=this.GetUserInfo.CompanyId;     
    },
    props:{
            folderId:{
                type:String,
                required: true
            },
            parentname:{
                type:String,
                required: true
            },
            folderName:{
                type:String,
                required: true
            },
            chkAddEditFolder:{
                type:String,
                required: true
            },
            parentId:{
                type:String,
                required: true
            },
            manageFolderPopupLabel:{
                type:String,
                required: true
            },
            docCode:{
                type:String,
                required: true
            },
            fileName:{
                type:String,
                required: true
            },
            fileId:{
                type:String,
                required: true
            },
            mySpaceOrOrganization:{
                type:Number,
                required: true
            },
            commonParentName:{
                type:String,
                required: true
            },
            breadcrumbForManagePopup:{
                type:String,
                required: true
            }
        },
        
    methods:{
        OpenPopup: function(){
            
           var vm =this;
           if(vm.chkAddEditFolder=="Add"){
            vm.$emit("showmodalmovepopups");
           }
           else{
            vm.ShowAlert("You can not change folder at the time of rename.","warning")
           }
          
        },
        
        closemanagepopup: function(){
            var vm=this;
            vm.$emit("closepopup")
        },
        BreadCrumb: function(){
            var vm=this;
            vm.$parent.BreadCrumb();
        },
        /*
        This method is used to save or edit folder
       */
        SaveFolder: function(folderIdmanage){
            var vm= this;
            if(vm.folderName==undefined || vm.folderName=="")
            {
                vm.errorMessage=vm.$t("FolderNameReqired");
                $("#FolderName").addClass("is-invalid");
                return false;
            }
            else{
                $("#FolderName").removeClass("is-invalid");
                $("#AddFolder").addClass("disabled");
            }
            let jsonobj={};
            if(this.chkAddEditFolder=="Add")
            {
                if(vm.mySpaceOrOrganization==1){
                    jsonobj={"FOLDERID":0,"FOLDERNAME":vm.folderName,"FOLDERDESC":"","PARENTID":vm.folderId==0?null:vm.folderId,"CREATED_BY":vm.loggeduserid,"COMPANY_ID":vm.companyId,"CONTAINER_NAME":null,"IsGlobal":1,"docBreadCrumb":vm.commonParentName+ '>' + vm.breadcrumbForManagePopup}
                }
                else{
                    jsonobj={"FOLDERID":0,"FOLDERNAME":vm.folderName,"FOLDERDESC":"","PARENTID":vm.folderId==0?null:vm.folderId,"CREATED_BY":vm.loggeduserid,"COMPANY_ID":vm.companyId,"CONTAINER_NAME":null,"IsGlobal":0,"docBreadCrumb":vm.commonParentName+ '>' + vm.breadcrumbForManagePopup}
                }
                
            }
            else{
                if(vm.mySpaceOrOrganization==1){
                    jsonobj={"FOLDERID":vm.folderId,"FOLDERNAME":vm.folderName,"FOLDERDESC":"","PARENTID":vm.parentId,"CREATED_BY":vm.loggeduserid,"COMPANY_ID":vm.companyId,"CONTAINER_NAME":0,"IsGlobal":1,"docBreadCrumb":vm.commonParentName+ '>' + vm.breadcrumbForManagePopup}
                }
                else{
                    jsonobj={"FOLDERID":vm.folderId,"FOLDERNAME":vm.folderName,"FOLDERDESC":"","PARENTID":vm.parentId,"CREATED_BY":vm.loggeduserid,"COMPANY_ID":vm.companyId,"CONTAINER_NAME":0,"IsGlobal":0,"docBreadCrumb":vm.commonParentName+ '>' + vm.breadcrumbForManagePopup}
                }
               
            }
            
             var objectPostString = JSON.stringify(jsonobj);
            DataService.ManageFolder(objectPostString).then((response)=>{
                if(response.data==0)
                {
                    if(this.chkAddEditFolder=="Add"){
                        
                        vm.ShowAlert(vm.$t("FolderAdd"), "success", true, vm.$t("Alert"));
                        if(vm.mySpaceOrOrganization==1){
                            vm.$parent.StorageDocumentListing(vm.folderId);
                        }
                        else{
                            if(vm.folderId==0){
                                vm.$parent.PagerData(1,vm.commonParentName);
                            }
                            else{
                                vm.$parent.StorageDocumentListing(vm.folderId);
                            }
                        }
                        
                    }
                    else{
                        vm.modalLabel="Edit Folder"
                        vm.ShowAlert(vm.$t("FolderUpdate"), "success", true,vm.$t("Alert"));
                        if(vm.folderId==0){
                            vm.$parent.StorageDocumentListing();
                        }
                        else{
                            vm.$parent.StorageDocumentListing(vm.parentId);
                        }
                        vm.$parent.GetFullTree();
                    }
                    vm.$emit("GetFullTree");
                    vm.$emit("closepopup");
                }
                else if(response.data==1){
                    vm.ShowAlert("Folder Already Exist", "failure", true, vm.$t('Alert'));
                      $("#AddFolder").removeClass("disabled");
                    //vm.$emit("closepopup");
                }
                else if(response.data==-2)
                {
                    vm.ShowAlert("Max limit 5", "failure", true, vm.$t('Alert'));
                    vm.$emit("closepopup");
                }
                else{
                    vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                }
            });

        },
         /*
        This method is used to edit file name
       */
        SaveFile: function(fileId,folderId,fileName){
            var vm=this;
            if(vm.fileName==undefined || vm.fileName=="")
            {
                vm.errorMessage="File Name is required.";
                $("#fileName").addClass("is-invalid");
                return false;
            }
            else{
                $("#fileName").removeClass("is-invalid");
                vm.$parent.GetFileDetails(fileId,null,fileName,folderId)
            }
           
        }
    }
}
</script>