<script>
import fileupload from "./../components/Common/file-upload.vue"
import DataService from "./../services/DataService"
import Pager from "./Common/Pager.vue"
export default {
    components: {
        Pager,
        fileupload
    },
    updated: function () {
        this.BindActionButtonEvent();
        this.onChange();
        this.CheckBoxBootstrap();
    },
    data() {
        return {
            pageNo: 1,
            pageSize: 10,
            sortExp: null,
            specificSortClick: null,
            listType: 'ListView',
            checkThis: this,
            pageSize: 10,
            isShowUploadDiv: false,
            searchCondition: null,
            // folderPermission:''
        }

    },
    props: {
        FilesListingData: {
            type: Array,
            required: true
        },
        parentname: {
            type: Array,
            required: true
        },
        commonParentName: {
            type: String,
            required: true
        },
        folderId: null,
        totalRecords: {
            type: Number,
            required: true
        },
        refCode:{
            type: String,
            required: true
        },
        routeClass:{
            type: String,
            required: false
        },
        TotalPages: {
            type: Number,
            required: true
        },
        folderPermission:{
            type: String,
            required: true
        }
    },
    methods: {
        // To pass variable 
        pagerMethod: function (s) {
           debugger
            // this.PageNumber = value.PageNumber;
            if (parseInt(s.PageNumber) > 0) {
                this.$parent.PagerData(parseInt(s.PageNumber), 'Organization Space',this.specificSortClick, this.sortExp, s.PageSize, this.folderId, this.searchCondition);
            }
            else {
                this.PageNo = 1
            }
        },
        ExpandCollapseList() {
            var element1 = document.getElementById("leftmenutree");
            element1.classList.toggle("left-menu-tab-collapsed");
            var element2 = document.getElementById("rightMenuTree");
            element2.classList.toggle("right-content-expand");
        },
        GetThumbnail: function(path){
            var cls=null;
            if(path!=null){
                var incStr = path.includes("wwwroot"); 
            }
            if(incStr==true){
                 cls=path.replace("/wwwroot", "");
            }
            else{
                cls=path;
            }
            return cls;
        },
        convertmbtoKb: function (val) {
            var convertKb = 0;
            if (val == 0) {
                convertKb = '0 Bytes'
            } 
            else if(val.toFixed(3)=="0.000")
            {
                var mbs=val* (1024 * 1024);
                convertKb = mbs.toFixed(2) + ' Bytes'
            }
            else
                if (val < 1) {
                    convertKb = val.toFixed(2) + ' KB'
                }
                else {
                    convertKb = val.toFixed(2) + ' MB'
                }
            return convertKb;
        },
         GetFileIcon: function(extnsn){
      var cls=null;
      if(extnsn=='mp3'){
        cls="fa fa-file-audio-o text-danger"
      }
      else if(extnsn=='mp4'){
         cls="fa fa-file-video-o text-info"
      }
      else if(extnsn=='docx'){
         cls="fa fa-file-word-o text-primary"
      }
      else if(extnsn=='pdf'){
         cls="fa fa-file-pdf-o"
      }
      else if(extnsn=='jpg' || extnsn=='png' || extnsn=='jpeg'){
         cls="fa fa-file-image-o"
      }
      else if(extnsn=="xls" || extnsn=="xlsx"){
          cls="fa fa-file-excel-o text-sucess"
      }
      else if(extnsn=="zip"){
          cls="fa fa-file-archive-o"
      }
      else if(extnsn=="ppt"){
          cls="fa fa-file-powerpoint-o text-primary"
      }
      else if(extnsn=="Programming"){
          cls="fa fa-file-code-o"
      }
      else if(extnsn=="Web"){
          cls="fa fa-globe text-primary"
      }
      else if(extnsn=="" || extnsn==null){
          cls="fa fa-folder text-primary"
      }
      else{
        cls="fa fa-file-o"
      }
      return cls;
    },
         openActionIcon(e) {
            e.target.nextSibling.removeAttribute('style');
            e.target.nextSibling.setAttribute('style', 'width:240px;');
            e.target.nextSibling.style.display = 'block';
        },
        closeActionIcon(e) {
            e.target.nextSibling.style.display = 'none';
        },
        SearchGlobalData: function (name, searchCond) {
            
            var vm = this;
            vm.$parent.StorageDocumentListing('', '', '', name, searchCond.trim());
        },
        RefreshPage: function(){
            var vm =this;
             window.location.reload();
        },
        pageSizecommon: function (e) {
            ;
            var vm=this;
            var scondition = ""
            if (vm.searchCondition == null) {
                scondition = vm.searchCondition
            }
            else {
                scondition = vm.searchCondition.trim();
            }
            this.pageSize = e.target.value;
            this.$emit("PagerData", vm.PageNo, vm.commonParentName, '', '', vm.pageSize, vm.folderId, scondition);
        },
        CloseUploadPopup: function () {
