            else{
                vm.isShowUploadDiv=false;
            }
        },
        GetFileIconClass: function (ext) {
            ext = ext.toLowerCase();
            var fileclass = "";
            if(this.listType=='ListView'){
                if (ext == 'png' || ext == "jpeg" || ext == "jpg" || ext == "JPG" || ext == "jfif" || ext == "pdf" || ext == "xlsx" || ext == "docx" || ext == "pptx") {
                    fileclass='d-none';
                }
                else if (ext == "txt") {
                    fileclass = "fa fa-file-text-o"
                }
                else if (ext == "xls") {
                    fileclass = "fa fa-file-excel-o"
                }
                else if (ext == "mp3") {
                    fileclass = "fa fa-file-audio-o"
                }
                else if (ext == "mp4") {
                    fileclass = "fa fa-file-video-o"
                }
                else {
                    fileclass = "fa fa-file-o"
                }
                return fileclass
            }
            else{
                if (ext == 'png' || ext == "jpeg" || ext == "jpg" || ext == "JPG" || ext == "jfif" || ext == "pdf" || ext == "xlsx" || ext == "docx" || ext == "pptx") {
                    fileclass='d-none';
                }
                else if (ext == "txt") {
                    fileclass = "fa fa-file-text-o pt-3"
                }
                else if (ext == "xls") {
                    fileclass = "fa fa-file-excel-o pt-3"
                }
                else if (ext == "mp3") {
                    fileclass = "fa fa-file-audio-o pt-3"
                }
                else if (ext == "mp4") {
                    fileclass = "fa fa-file-video-o pt-3"
                }
                else {
                    fileclass = "fa fa-file-o pt-3"
                }
                return fileclass
            }
        },
        updatelistafterupload(folderId) {
            this.$emit("StorageDocumentListing", folderId);
        }
    }
}
