<template>
    <div class="my-popups">
        <div class="modal d-block ">
            <div class="modal-dialog">
                <div class="modal-content">
                    <div class="modal-header ui-draggable-handle">
                        <h4 name="header" class="modal-title">
                            Select Parent Folder
                        </h4>
                        <button @click="closepopup" class="close"><em aria-hidden="true"
                                class="fa fa-times"></em> </button>
                    </div>
                    <div class="">
                        <div id="main" class="collapse show px-3 py-2 border FullTreeData"
                            style="max-height:90vh; overflow-y: auto;">
                            <DocCommonTreeView  :mySpaceOrOrganization="mySpaceOrOrganization" :FolderIdMoveLeftMenu="FolderIdMoveLeftMenu" :organizationSpace="organizationSpace" :MySpaceFolders="FindalFolderArray" @FolderIdMoveLeftMenu="FolderIdMoveLeftMenu"></DocCommonTreeView>
                            <!-- <DocCommonTreeView v-if='mySpaceOrOrganization==1' :FolderIdMoveLeftMenu="FolderIdMoveLeftMenu" :organizationSpace="organizationSpace" @FolderIdMoveLeftMenu="FolderIdMoveLeftMenu"></DocCommonTreeView> -->
                            <!-- <ul id='FolderFullTree' class='filetree treeview'>
                                <li class='collapsable' v-if='mySpaceOrOrganization!=1'>
                                    <div class='hitarea collapsable-hitarea'></div>
                                    <span class='folder'>
                                        <a class='folderlink' href='javascript:;'>My Space</a>
                                    </span>
                                    <ul id='FolderFullTree' class='filetree treeview'>
                                        <ChildFolder  @FolderIdMoveLeftMenu="FolderIdMoveLeftMenu" :FolderIdMoveLeftMenu="FolderIdMoveLeftMenu"
                                            :FolderTree="FindalFolderArray"></ChildFolder>
                                    </ul>
                                </li>
                            </ul>
                            <ul id='FolderFullTree' class='filetree treeview'>
                            <li class='collapsable' v-if='mySpaceOrOrganization==1'>
                                <div class='hitarea collapsable-hitarea'></div>
                                <span class='folder'>
                                    <a class='folderlink' href='javascript:;'>Organization Space</a>
                                </span>
                                <ul id='FolderFullTree' class='filetree treeview'>
                                    <ChildFolder :organizationSpace="organizationSpace" :FolderIdMoveLeftMenu="FolderIdMoveLeftMenu" @FolderIdMoveLeftMenu="FolderIdMoveLeftMenu" @FolderBreadscrmb="FolderBreadscrmb"
                                        
                                        ></ChildFolder>
                                </ul>
                            </li>
                        </ul> -->
                        </div>
                    </div>
                    <div class="modal-footer text-right pr-0 pb-0">
                        <!-- <button title="Search" @click="savebreadcrumb"
                            class="popupgreenbtn btn btn-success formbtn widthhalf pr-2">Confirm</button> -->
                        <button title="Cancel" @click="closepopup"
                            class="popupredbtn btn btn-danger formbtn widthhalf pr-2"><em aria-hidden="true"
                                class="fa fa-times pr-2"></em> Cancel</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import ChildFolder from "./ChildFolder.vue";
import DataService from "../services/DataService";
import DocCommonTreeView from "./DocCommonTreeView.vue";
export default {
    components: {
    ChildFolder,
    DocCommonTreeView
},
    props:{
        FolderIdMoveLeftMenu:{
            type: Function,
            required: true
        },
        mySpaceOrOrganization:{
            type: Number,
            required: true
        },
        folderids: {
            type: String,
            required: true
        }
    },
    data() {
        return {
            loggeduserid: null,
            folderarray: [],
            FindalFolderArray: [],
            FilesListingData: [],
            folderTreeList: [],
            sharedFolder: [],
            folderidManagePopup: null,
            organizationSpace:[]
        }
    },
    created: function () {
        $("body").on("click", "#FolderFullTree .folderlink", function () {
            $(".folderlink").removeClass("selectedfolder");
            $(this).addClass("selectedfolder");
        });
        this.loggeduserid = this.GetUserInfo.ID;
        this.GetFullTree();
    },
    mounted: function () {
        $("body").on("click", "#FolderFullTree .folderlink", function () {
            $(".folderlink").removeClass("selectedfolder");
            $(this).addClass("selectedfolder");
        });
    },
    methods: {
        GetFullTree() {
            
            var vm = this;
            DataService.FolderFullTreeView().then((response) => {
                response.data.folderTreeList.forEach((value, index) => {
                    //vm.sharedfolders(value)
                    if (value.OWNER_USER_ID == this.loggeduserid && value.IsGlobal == 0) {
                        var CheckIfExist = vm.FindalFolderArray.filter(function (el) {
                            return el.FOLDER_ID == value.FOLDER_ID;
                        });


                        if (CheckIfExist.length == 0) {
                            if (value.FOLDER_PARENT_ID == null) {
                                let folderId = value.FOLDER_ID
                                let rslt = vm.getChildFolder(folderId, value, response.data.folderTreeList)
                                vm.FindalFolderArray.push(rslt)
                            }
                        }

                    }

                });
                vm.GetOrganizationSpace();
                vm.folderTreeList = response.data.folderTreeList;
                vm.SharedUserList = response.data.userList;
                vm.sharedFolder = response.data.sharedTreeList;
            });
        },
        getChildFolder: function (folderId, currentItem, data) {
            let ths = this;
            let retrunArray = currentItem
            var childArray = data.filter(function (el) {
                return el.FOLDER_PARENT_ID == folderId;
            });
            let finalChildArray = [];
            childArray.forEach((value, index) => {
                let ChildfolderId = value.FOLDER_ID
                var CheckIfExist1 = finalChildArray.filter(function (el) {
                    return el.FOLDER_ID == value.FOLDER_ID;
                });
                if (CheckIfExist1.length == 0) {
                    var CheckIfExist = data.filter(function (el) {
                        return el.FOLDER_PARENT_ID == ChildfolderId;
                    });
                    if (CheckIfExist != null && CheckIfExist.length > 0) {
                        let rslt = ths.getChildFolder(ChildfolderId, value, data);
                        finalChildArray.push(rslt);
                    } else {
                        finalChildArray.push(value);
                    }
                }
            });
            retrunArray.chidFolder = finalChildArray;
            return retrunArray
        },
        GetOrganizationSpace: function () {
            
            var vm = this;
            DataService.GetOrganizationFolderTreeData().then((response) => {
                response.data.folderTreeList.forEach((value, index) => {
                    var CheckIfExist = vm.organizationSpace.filter(function (el) {
                        return el.FOLDER_ID == value.FOLDER_ID;
                    });


                    if (CheckIfExist.length == 0) {
                        if (value.FOLDER_PARENT_ID == null) {
                            let folderId = value.FOLDER_ID
                            let rslt = vm.getsharedChildFolder(folderId, value, vm.folderTreeList);
                            vm.organizationSpace.push(rslt)
                        }
                    }
                });
            });


        },
        getsharedChildFolder: function (folderId, currentItem, data) {
            
            let ths = this;
            let retrunArray = currentItem
            var childArray = data.filter(function (el) {
                return el.FOLDER_PARENT_ID == folderId;
            });
            let finalChildArray = [];
            childArray.forEach((value, index) => {
                let ChildfolderId = value.FOLDER_ID
                var CheckIfExist1 = finalChildArray.filter(function (el) {
                    return el.FOLDER_ID == value.FOLDER_PARENT_ID;
                });
                if (CheckIfExist1.length == 0) {
                    var CheckIfExist = data.filter(function (el) {

                        return el.FOLDER_PARENT_ID == ChildfolderId;


                    });
                    if (CheckIfExist != null && CheckIfExist.length > 0) {
                        let rslt = ths.getsharedChildFolder(ChildfolderId, value, data);
                        finalChildArray.push(rslt);
                    } else {
                        finalChildArray.push(value);
                    }
                }
            });
            retrunArray.chidFolder = finalChildArray;
            return retrunArray
        },
        closepopup: function () {
            
            this.$emit("closepopupmovefolder");
        },
        savebreadcrumb: function () {
            var vm=this;
            this.$emit("BreadCrumb",vm.folderidmanagePopup)
            this.$emit("closepopup");
        },
        FolderIdMoveLeftMenu(data,parentid,actionVal,name) {
            
            var vm=this;
            vm.folderidmanagePopup = name;
            this.$emit("FolderIdManagePopup", data,parentid,actionVal,name)
        }
    }
}
</script>