<template>
    <div>
    <section>
        <loader :is-visible="isLoading"></loader>
        <LeftMenu :refCode="refCode" :routeClass="routeClass" :organizationSpace="organizationSpace" :RecentPinnedFolderArray="RecentFolderArray" 
        :checkActionType="checkActionType"  v-on:PagerData="PagerData"
            :pinnedFolderArray="pinnedFolderArray" v-on:BreadCrumb="BreadCrumb" :sharedChildFolder="sharedChildFolder"
            :folderTreeList="folderTreeList" v-on:StorageDocumentListing="StorageDocumentListing"
            :StorageDocumentListing="StorageDocumentListing" :userid="loggeduserid" :SharedFolder="SharedFolderParent"
            :UserList="SharedUserList" :FolderArray="FindalFolderArray" v-on:FilesListing="FilesListing" :FilesListingData="FilesListingData" :addClsLeftMenu="addClsLeftMenu"></LeftMenu>
        <section id="main-content" class="main-content">
            <section class="main-content">
                <div class="container-fluid p-0">
                    <div class="right-content" id="rightMenuTree">
                        <!-- <RightContent/> -->
                        <!-- <Listing @ManageOpen="ManageOpen" @ModalAddGroup="ModalAddGroup" @ModalGroupListing="ModalGroupListing" :MarkAsFavourite="MarkAsFavourite" :MarkAsUnpins="MarkAsUnpins" :user="loggeduserid" :sharedwith="SharedWith" :listingdata="FilesListingData"  :folderSummary="folderSummary" :loggeduserid="loggeduserid"></Listing>                  -->
                        <Listing :storePageNo="storePageNo" :showUploadDiv="showUploadDiv"
                            v-if="isShowMySpaceListing == true || isShowSharedListing == true"
                            :commonParentName="commonParentName" :folderPermission="folderPermission"
                            :isShowMySpaceListing="isShowMySpaceListing" :isShowSharedListing="isShowSharedListing"
                          
                            :sharedFilesListingData="sharedFilesListingData" v-on:ModalmoveFolder="ModalmoveFolder"
                            v-on:PagerData="PagerData" :parentname="parentname" :TotalPages="TotalPages"
                            :totalRecords="totalRecords" v-on:StorageDocumentListing="StorageDocumentListing"
                            :folderId="folderId" v-on:GetFolderDetail="GetFolderDetail" v-on:ManageOpen="ManageOpen"
                            :MarkAsFavourite="MarkAsFavourite" :MarkAsUnpins="MarkAsUnpins" :user="loggeduserid"
                            :sharedwith="SharedWith" :listingdata="FilesListingData" :folderSummary="folderSummary"
                            :loggeduserid="loggeduserid"  v-on:GetFullTree="GetFullTree" :PageNumber="pageNo" :routeClass="routeClass" :refCode="refCode" ref="listing" :listingView="listingView">
                        </Listing>
                        <ManageFolder :commonParentName="commonParentName" :PagerData="PagerData"
                            :breadcrumbForManagePopup="breadcrumbForManagePopup"
                            v-on:showmodalmovepopups="showmodalmovepopups" :mySpaceOrOrganization="mySpaceOrOrganization"
                            :fileId="fileId" :fileName="fileName" :docCode="docCode"
                            :manageFolderPopupLabel="manageFolderPopupLabel" :parentId="parentId"
                            :chkAddEditFolder="chkAddEditFolder" v-on:BreadCrumb="BreadCrumb" :folderName="folderName"
                            v-on:GetFullTree="GetFullTree" :folderId="folderId" :parentname="parentname"
                            v-on:ManageFolders="ManageFolders" v-on:closepopup="closepopup" v-if="showmodal == true">
                        </ManageFolder>
                        <docShare v-on:StorageDocumentListing="StorageDocumentListing" v-on:FilesListing="FilesListing" v-on:BreadCrumb="BreadCrumb" :folderName="folderName"
                            :docCode="IsDocCode" :idFile="idFile" :folderId="folderId" :parentname="parentname"
                            v-on:ManageFolders="ManageFolders" :generateJSONData=generateJSONData v-on:closepopup="closepopup"
                            v-if="sharemodal == true">
                        </docShare>
                        <GlobalListing v-if="isShowGlobalSpace == true" :TotalPages="TotalPages" :folderId="folderId"
                        v-on:StorageDocumentListing="StorageDocumentListing" v-on:GetOrganizationSpace="GetOrganizationSpace"
                           :totalRecords="totalRecords" :folderPermission="folderPermission"
                            :PagerData="PagerData" :commonParentName="commonParentName" v-on:PagerData=PagerData
                            v-on:GetFolderDetail="GetFolderDetail" :parentname="parentname" :ManageOpen="ManageOpen"
                            :FilesListingData="FilesListingData"  :routeClass="routeClass" :refCode="refCode" ref="listing"></GlobalListing>
                    </div>
                </div>
            </section>
        </section>
    </section>
    <ModalAddGroup v-on:ModalAddGroups="ModalAddGroups" v-on:CloseAddGroupPopup="CloseAddGroupPopup" v-if="ShowModalAddGroup">
    </ModalAddGroup>
    <ModalAddTag v-on:StorageDocumentListing="StorageDocumentListing" :folderId="folderId" :selectedtags="selectedTags" v-on:ManageTagopen="ManageTagopen"
    v-on:ClosePopupTag="ClosePopupTag" v-if="showmodalTag == true"></ModalAddTag>
    <ModalMoveFolderFiles :FolderArray="FindalFolderArray" :mySpaceOrOrganization="mySpaceOrOrganization" :organizationSpace="organizationSpace" :checkActionType="checkActionType" :moveObj="moveObj"
    v-on:FolderIdmove="FolderIdmove" v-on:ModalmoveFolder="ModalmoveFolder" v-on:StorageDocumentListing="StorageDocumentListing"
        v-if="showmovefolderpopup == true" v-on:MoveFoldersFiles="MoveFoldersFiles" v-on:CloneFoldersFiles="CloneFoldersFiles">
    </ModalMoveFolderFiles>
    <Ownertree :mySpaceOrOrganization="mySpaceOrOrganization" :organizationSpace="organizationSpace"
    v-on:FolderIdManagePopup="FolderIdManagePopup" v-on:closepopupmovefolder="closepopupmovefolder"
        v-if="showmodalmovepopup == true"></Ownertree>
    <ModalSaveTagPopup v-if="saveTagPopupShow == true" :isDivActiveFromTagListing="isDivActiveFromTagListing" :isDivActive="isDivActive" v-on:OpenTagPopUp="OpenTagPopUp"
    v-on:closeAddTagPopup="closeAddTagPopup">
    </ModalSaveTagPopup>
</div>
</template>
<script>

import LeftMenu from "@/components/LeftMenu.vue"
import Ownertree from "@/components/OwnerTree.vue"
import RightContent from "@/components/RightContent.vue"
import Listing from "@/components/FIlesListing.vue"
import DataService from "../../services/DataService";
import ManageFolder from "@/views/UI/Manage.vue"
import ModalAddGroup from "./Modal/ModalAddGroup.vue"
import docShare from "@/views/UI/ShareDocument.vue"
import ModalAddTag from "./Modal/ModalAddTag.vue"
import ModalMoveFolderFiles from "./Modal/ModalMoveFolderFiles.vue"
import GlobalListing from "@/components/GlobalSpaceListing.vue"
import DocCommonTreeView from "../../components/DocCommonTreeView.vue";
import ModalSaveTagPopup from "./Modal/ModalSaveTagPopup.vue";
export default {
    components: {
        LeftMenu,
        RightContent,
        Listing,
        ManageFolder,
        ModalAddGroup,
        docShare,
        ModalAddTag,
        ModalMoveFolderFiles,
        GlobalListing,
        Ownertree,
        DocCommonTreeView,
        ModalSaveTagPopup
    },
    data() {
        return {
            isLoading: false,
            loggeduserid: null,
            companyId: null,
            SharedUserList:[],
            folderarray: [],
            FindalFolderArray: [],
            FilesListing:[],
            FilesListingData: [],
            folderTreeList: [],
            sharedFolder: [],
            SharedFolderParent: [],
            SharedWith: [],
            folderId: null,
            showmodal: false,
            selectedTags:'',
            showmodalTag: false,
            ShowModalAddGroup: false,
            ShowGruopListing: false,
            folderSummary: [],
            parentname: [],
            folderDetails: [],
            folderName: null,
            chkAddEditFolder: null,
            pageNo: 1,
            pageSize: 10,
            TotalPages: 0,
            totalRecords: 0,
            parentId: null,
            sharemodal: false,
            showmovefolderpopup: false,
            moveFolderObj: [],
            folderFileId: "",
            manageFolderPopupLabel: "",
            docCode: 0,
            fileName: null,
            fileId: null,
            sharedparentfolder: [],
            sharedChildFolder: [],
            sharedFilesListingData: [],
            isShowSharedListing: false,
            isShowMySpaceListing: true,
            folderPermission: "",
            recentFolderList: [],
            pinnedFolderList: [],
            RecentFolderArray: [],
            pinnedFolderArray: [],
            organizationSpace: [],
            mySpaceOrOrganization: null,
            commonParentName: "My Space",
            isShowGlobalSpace: false,
            breadcrumbForManagePopup: "",
            refCode: 'MySpace',
            showmodalmovepopup: false,
            checkActionType: '',
            showUploadDiv: false,
            generateJSONData: [],
            sourceIds: '',
            IsDocCode: '',
            moveObj: [],
            saveTagPopupShow: false,
            yourobj:[],
            routeClass:'My Space',
            isDivActiveFromTagListing:false,
            isDivActive:false,
            storePageNo:null,
            Name:'',
            listingView:'ListView',
            str:'',
            addClsLeftMenu:false
        }
    },
    created: function () {
        debugger
        var vm = this;
        const queryString = window.location.search;
        const urlParams = new URLSearchParams(queryString);
        const commonparentname = urlParams.get('ParentName')
        const folderid = urlParams.get('FolderId')
        const refcode=urlParams.get('RefCode');
        const addclass=urlParams.get('AddClass');
        var pathname = window.location;
        vm.folderId = vm.$route.params.FolderId==undefined?folderid:vm.$route.params.FolderId;
        vm.loggeduserid = vm.GetUserInfo.ID;
         vm.routeClass=vm.$route.params.AddClass==undefined?addclass:vm.$route.params.AddClass;
         if(window.location.search.split("=")[2]==undefined){
            vm.commonParentName = vm.$route.params.ParentName==undefined?"My Space":vm.$route.params.ParentName;
         }
         else{
            vm.commonParentName = vm.$route.params.ParentName==undefined?commonparentname.replace("+"," "):vm.$route.params.ParentName;
         }
        if(refcode==null){
            vm.refCode=vm.$route.params.RefCode==undefined?"MySpace":vm.$route.params.RefCode;
        }
        else{
            vm.refCode=vm.$route.params.RefCode==undefined?refcode:vm.$route.params.RefCode;
        }
        if( vm.refCode=="Shared"){
            vm.StorageDocumentListing(vm.folderId);
        }
        vm.companyId = vm.GetUserInfo.CompanyId;
        vm.organizationSpace=[];
        vm.GetFullTree();
        vm.StorageDocumentListing(vm.folderId);
        vm.GetSharedFolder();
    },
    mounted: function () {
        $("#FolderFullTree").treeview();
        $("body").on("click", "#FolderFullTree .folderlink", function () {
            $("#FolderFullTree .folderlink").removeClass("selectedfolder");
            $(this).addClass("selectedfolder");
        });
    },
    updated: function () {

        this.BindActionButtonEvent();
    },
    methods: {
        // Files and Folder Listing 
        StorageDocumentListing(folderiddata, Parentid, actionVal, name, searchcondition, sortBy, sortExp, listingCode, sharedPermission,pageno,clikType) {
            debugger
            var vm = this;
            const queryString = window.location.search;
            const urlParams = new URLSearchParams(queryString);
            vm.routeClass=null;
            vm.isLoading = true;
            if (name != undefined) {
                vm.commonParentName = name;
            }
            if (listingCode != undefined) {
                vm.refCode = listingCode;
            }
            if (listingCode == "MySpace" || listingCode == "Recent" || listingCode == "Pinned") {
                vm.isShowGlobalSpace = false;
                vm.isShowMySpaceListing = true;
                vm.isShowSharedListing = false;
                vm.mySpaceOrOrganization = "";
                 vm.routeClass='a'+folderiddata;
            }
            if (listingCode == "Organization" || vm.commonParentName=="Organization Space" || name == "Organization Space" || actionVal == 1) {
                vm.isShowGlobalSpace = true;
                vm.isShowMySpaceListing = false;
                vm.isShowSharedListing = false;
                vm.mySpaceOrOrganization = 1;
                vm.showUploadDiv = false;
                 vm.routeClass='a'+folderiddata;
            }
            if (listingCode == "Shared" || name == "Shared" || vm.commonParentName=="Shared Folders" ) {
                vm.isShowSharedListing = true;
                vm.isShowMySpaceListing = false;
                vm.isShowGlobalSpace = false;
                vm.showUploadDiv = false;
                 vm.routeClass='a'+folderiddata;
            }
            if (listingCode == "Recent" || listingCode == "Pinned") {
                vm.showUploadDiv = false;
            }
            if (listingCode == "MySpace") {
                vm.showUploadDiv = true;
            }
            if ((folderiddata!=0 || folderiddata=="")  && vm.commonParentName!=""){
                this.listingView='ListView'
            }
            vm.folderPermission = sharedPermission==undefined?vm.folderPermission:sharedPermission;
            vm.totalRecords = 0;
            vm.folderId = folderiddata;
            vm.addClsLeftMenu=true;
            if(clikType!='Tree'){
                vm.GetFullTree();
            }
            vm.parentId = Parentid;
            if (folderiddata == undefined || folderiddata == '' ||  folderiddata == '0') {
                folderiddata = 0;
                vm.addClsLeftMenu=false;
                vm.pageNo=pageno==undefined?vm.pageNo:pageno;
                vm.storePageNo=pageno==undefined?vm.pageNo:pageno;
            }
            else if(urlParams.get('FolderId')==vm.folderId ){
                vm.pageNo=pageno==undefined || pageno==""?vm.pageNo:pageno;
            }
            else{
                vm.pageNo=1;
            }
            var params = `folderId=${folderiddata}&pageNo=${vm.pageNo}&pageSize=${vm.pageSize}&searchFilter=${searchcondition}&sortBy=${sortBy}&sortExp=${sortExp}&refCode=${vm.refCode}`;
            if (searchcondition == undefined) {
                params = `folderId=${folderiddata}&pageNo=${vm.pageNo}&pageSize=${vm.pageSize}&searchFilter=&sortBy=${sortBy}&sortExp=${sortExp}&refCode=${vm.refCode}`;
            }
            DataService.GetFolderList(params).then((response) => {
                vm.isLoading = false;
                vm.FilesListingData = response.data.FolderData;
                if(vm.FilesListingData!=undefined){
                    if (vm.FilesListingData.length > 0) {
                    vm.totalRecords = vm.FilesListingData[0].totalRecords;
                }
                
                
                if(vm.refCode=='Shared'){
                    vm.FilesListingData.forEach(function (item, index){
                      vm.folderPermission= vm.FilesListingData[index].PERMISSION;
                    });

                }
                vm.TotalPages = Math.ceil(vm.totalRecords / this.pageSize);
                vm.BreadCrumb(vm.commonParentName, folderiddata,Parentid);
            }
                this.$refs.listing.ShowUpload(true);
                setTimeout(function () {
                    vm.ResponsiveDataTable('tblFileList')
                }, 500);
                setTimeout(function () { vm.CheckBoxBootstrap() }, 100);
            })

        },
        //Folder Listing with sub folder 
        GetFullTree() {
            var vm = this;
            vm.FindalFolderArray=[]
            DataService.FolderFullTreeView().then((response) => {
                response.data.folderTreeList.forEach((value, index) => {
                    if (value.OWNER_USER_ID == vm.loggeduserid && value.IsGlobal == 0) {
                        var CheckIfExist = vm.FindalFolderArray.filter(function (el) {
                            return el.FOLDER_ID == value.FOLDER_ID;
                        });

                        if (CheckIfExist.length == 0) {
                            if (value.FOLDER_PARENT_ID == null) {
                                let folderId = value.FOLDER_ID
                                let rslt = vm.getChildFolder(folderId, value, response.data.folderTreeList)
                                vm.FindalFolderArray.push(rslt)
                            }
                        }
                    }
                });
                vm.folderTreeList = response.data.folderTreeList;
                vm.sharedFolder = response.data.sharedTreeList;
                vm.GetSharedFolder();
                vm.GetRecentlyAddPinnedFolder();
                vm.organizationSpace=[];
                vm.GetOrganizationSpace();
            });
        },

        GetSharedFolder: function () {
            var vm = this;
            DataService.GetSharedFolderTree().then((response) => {

                vm.sharedparentfolder = response.data.SharedFolderData;
                 vm.str="";
                vm.sharedparentfolder.forEach((value, index) => {
                    if (value.SHARED_USER_ID == vm.loggeduserid) {
                        var CheckIfExist = vm.sharedChildFolder.filter(function (el) {
                            return el.FOLDER_ID == value.FOLDER_ID;
                        });


                        if (CheckIfExist.length == 0) {
                            
                           
                            if (value.FOLDER_PARENT_ID == null) {
                                let folderId = value.FOLDER_ID
                                vm.str+=folderId + ',';
                                let rslt = vm.getsharedChildFolder(folderId, value, vm.folderTreeList);
                                vm.sharedChildFolder.push(rslt)
                            }
                            else{
                                let folderId = value.FOLDER_ID
                                if((vm.str.includes(folderId))==false){
                                   let rslt = vm.getsharedChildFolder(folderId, value, vm.folderTreeList);
                                    vm.sharedChildFolder.push(rslt)
                                }
                               
                            }
                           
                        }

                    }

                });

            });
        },
        getsharedChildFolder: function (folderId, currentItem, data) {
            
            let ths = this;
            let retrunArray = currentItem
            var childArray = data.filter(function (el) {
                return el.FOLDER_PARENT_ID == folderId;
            });
            let finalChildArray = [];
            childArray.forEach((value, index) => {
                let ChildfolderId = value.FOLDER_ID
                ths.str+=ChildfolderId +',';
                var CheckIfExist1 = finalChildArray.filter(function (el) {
                    return el.FOLDER_ID == value.FOLDER_PARENT_ID;
                });
                if (CheckIfExist1.length == 0) {
                    var CheckIfExist = data.filter(function (el) {

                        return el.FOLDER_PARENT_ID == ChildfolderId;


                    });
                    if (CheckIfExist != null && CheckIfExist.length > 0) {
                        let rslt = ths.getsharedChildFolder(ChildfolderId, value, data);
                        finalChildArray.push(rslt);
                    } else {
                        finalChildArray.push(value);
                    }
                }
            });
            retrunArray.chidFolder = finalChildArray;
            return retrunArray
        },
        MarkAsFavourite: function (jsonResult) {
            debugger
            var vm = this;
            vm.isLoading = true;
            vm.yourobj=[];
            let obj = {}
            vm.MarkAsFavoriteWithButton = true;
            var val = "";
            // var data = { jsonResult};  

          
            var CheckIfExist = jsonResult.filter(function (el) {
                return el.pinId == 0;
            });
            if (CheckIfExist.length > 0) {
                CheckIfExist.forEach((item, index) => {
                    let obj = {
                        Id: item.Id,
                        ModuleName: 'DocStorage',
                        ActionType: 'Pin'
                    }
                    vm.yourobj.push(obj)
                });
            }


            let pinunpinObj = JSON.stringify(vm.yourobj);
            if (CheckIfExist.length > 0) {
                DataService.ManageFolderPin_UpPin(pinunpinObj).then((response) => {

                    if (response.data.status == 'Success') {
                        $('.chkItems:checkbox:checked,#chkAll_0').prop("checked", false).removeAttr('checked');
                        vm.ShowAlert(vm.$t('FolderPinSucces'), "success", true, vm.$t("Alert"));
                        vm.StorageDocumentListing(vm.folderId);
                        vm.GetFullTree();
                        vm.GetSharedFolder();
                        vm.GetRecentlyAddPinnedFolder();
                        vm.GetOrganizationSpace();
                        vm.yourobj = [];
                        vm.isLoading = false;
                    }
                })
            }
            else {
                vm.ShowAlert(vm.$t('PinUnPinWarning', 'sdgf'), "warning", true, vm.$t("Alert"));
                
                vm.yourobj = [];
                vm.isLoading = false;
            }
        },
        MarkAsUnpins: function (val) {
            
            var vm = this;
            vm.isLoading = true;
                    let obj = {
                Id: val,
                        ModuleName: 'DocStorage',
                        ActionType: 'UnPin'
                    }
            let yourobj = JSON.stringify(obj);
            DataService.ManageFolderPin_UpPin(yourobj).then((response) => {

                if (response.data.status == 'Success') {
                    vm.ShowAlert(vm.$t('FolderUnpinned'), "success", true, vm.$t("Alert"));
                    vm.StorageDocumentListing(vm.folderId);
                    vm.GetFullTree();
                    vm.GetRecentlyAddPinnedFolder();
                    vm.GetOrganizationSpace();
                    vm.isLoading = false;
                }
            })
        },
        //Get Child Folder
        getChildFolder: function (folderId, currentItem, data) {

            let ths = this;
            let retrunArray = currentItem
            var childArray = data.filter(function (el, index) {
                return el.FOLDER_PARENT_ID == folderId;
            });
            let finalChildArray = [];
            childArray.forEach((value, index) => {
                let ChildfolderId = value.FOLDER_ID
                var CheckIfExist1 = finalChildArray.filter(function (el) {
                    return el.FOLDER_ID == value.FOLDER_ID;
                });
                if (CheckIfExist1.length == 0) {
                    var CheckIfExist = data.filter(function (el, index) {
                        return el.FOLDER_PARENT_ID == ChildfolderId;
                    });
                    if (CheckIfExist != null && CheckIfExist.length > 0) {
                        let rslt = ths.getChildFolder(ChildfolderId, value, data);
                        finalChildArray.push(rslt);
                    } else {
                        finalChildArray.push(value);
                    }
                }
            });
            retrunArray.chidFolder = finalChildArray;
            return retrunArray
        },
        ModalAddGroup: function () {
            var vm = this;
            vm.ShowModalAddGroup = true;
        },
        showmodalmovepopups: function () {

            var vm = this;
            vm.showmodalmovepopup = true
        },
        FolderIdManagePopup: function (data, parentid, actionVal, name) {

            var vm = this;
            vm.folderIdmanage = data;
            vm.StorageDocumentListing(data, parentid, actionVal, name);
            vm.showmodalmovepopup = false;
        },
        closepopupmovefolder: function () {
            this.showmodalmovepopup = false;
        },
        CloseAddGroupPopup: function () {
            var vm = this;
            vm.ShowModalAddGroup = false;
            vm.ShowGruopListing = false;
        },
        CloseListingPopup: function () {
            var vm = this;
            vm.ShowModalAddGroup = false;
            vm.ShowGruopListing = false;
        },
        ModalGroupListing: function () {
            var vm = this;
            vm.ShowGruopListing = true;
        },
        //Add Folder open pop up
        ManageOpen: function (data, actionVal) {

            var vm = this;
            vm.showmodal = true
            //vm.mySpaceOrOrganization=actionVal;
            if (data == null) {
                vm.manageFolderPopupLabel = vm.$t("EditFolder")
            }
            else if (data == 0) {
                vm.manageFolderPopupLabel = "Edit File"
            }
            else {
                vm.manageFolderPopupLabel = vm.$t("AddFolder");
            }
            this.chkAddEditFolder = data
        },
        //Add Tag open pop up
        ManageTagopen: function (tagIds) {

            var vm = this;
            vm.selectedTags=tagIds;
            vm.showmodalTag = true;
        },
        //Add Folder close pop up
        closepopup: function () {
            
            var vm = this;
            //this.parentname = ""
            vm.folderName = ""
            vm.folderId = ""
            vm.docCode = ""
            //vm.parentname=""
            vm.showmodal = false;
            vm.sharemodal = false;
        },
        ClosePopupTag: function () {

            var vm = this;
            vm.selectedTags='';
            vm.showmodalTag = false
        },
        closePopupGroupListing: function () {

            var vm = this;
            vm.sharemodal = false;

        },
        //Showing breadcrumb 
        BreadCrumb(name, id,pid) {
            
            var vm = this;
            if(pid==null || pid==""){
                pid=0;
            }
            if (id != undefined) {
                vm.folderId = id;
            }
          
            vm.commonParentName = name;
            if (vm.folderId == undefined || vm.folderId == "") {
                vm.folderId = 0;
            }
            var params = `folderId=${vm.folderId}&breadCrumb=&parentId=${pid}`;
            DataService.FolderBreadCrumbs(params).then((response) => {
                vm.parentname = [];
                var numbersString = response.data;
                var numbersArray = numbersString.split('>');
                numbersArray.forEach((value, index) => {
                    vm.parentname.push(value.split("~"))
                })
                vm.closepopup;
                DataService.GetParentFolderBreadCrumb(vm.folderId).then((response) => {
                    vm.breadcrumbForManagePopup = response.data;
                })
            });

        },
        //Get Edit Folder Data
        GetFolderDetail(fid, doc, parentId, actionVal) {

            var vm = this;
            var params = ""
            vm.mySpaceOrOrganization = actionVal;
            vm.parentId = parentId;
            if (fid != 0) {
                vm.folderId = fid
            }
            else {
                vm.folderId;
            }
            DataService.GetStorageFolderDetails(vm.folderId).then((response) => {
                vm.folderDetails = JSON.parse(response.data);
                vm.folderDetails.FolderJsonData.forEach((item, index) => {

                    //vm.breadcrumbForManagePopup = item.storage_folder_name;
                    vm.folderName = item.storage_folder_name
                })
            })
        },
        //Pagination
        PagerData: function (pagesize, actionVal, sortBy, sortExp, pagecountlist, folderid, searchcondition) {
            
            var vm = this;
            vm.pageNo = pagesize;
            vm.folderId = folderid;
            vm.pageSize = pagecountlist == undefined ? vm.pageSize : pagecountlist;
             if(searchcondition==undefined){
                searchcondition="";
             }
            if (actionVal == "Organization Space") {
                vm.StorageDocumentListing(vm.folderId, '', 1, actionVal, searchcondition, sortBy, sortExp, 'Organization','',vm.pageNo);
            }
            else if (actionVal == "My Space") {
                vm.StorageDocumentListing(vm.folderId, '', '', actionVal, searchcondition, sortBy, sortExp, 'MySpace','',vm.pageNo);
            }
            else if (actionVal == "Recently Added") {
                vm.StorageDocumentListing(vm.folderId, null, null, actionVal, searchcondition, sortBy, sortExp, 'Recent','',vm.pageNo);
            }
            else if (actionVal == "Pinned Folders") {
                vm.StorageDocumentListing(vm.folderId, null, null, actionVal, searchcondition, sortBy, sortExp, 'Pinned','',vm.pageNo);
            }
            else if (actionVal == "Shared Folders") {
                vm.StorageDocumentListing(vm.folderId, null, null, actionVal, searchcondition, sortBy, sortExp, 'Shared','',vm.pageNo);
            }
           
        },
        ModalmoveFolder: function (id, dcode, moveFolderObj) {
            
            var vm = this;
            vm.folderId = 0;
            vm.checkActionType = 'Move'
            if (dcode != undefined) {
                vm.folderFileId = id;
            }

            vm.doccode = dcode;
            vm.moveObj = moveFolderObj
            vm.showmovefolderpopup = true;
        },
        closepopupFolder: function () {
            this.checkActionType = ''
            this.showmovefolderpopup = false;
        },
        FolderIdmove: function (fid, parentid) {
            debugger
            var vm = this;
            vm.folderId = fid;
        },
        MoveFoldersFiles: function () {
            
            var vm = this;
            var val = "";
            vm.moveFolderObj=[]
            if (vm.folderFileId != null) {
                val = vm.folderFileId;
            }
            if (vm.folderFileId == null || vm.folderFileId == "") {
                $('#tblFileList .chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {

                    vm.doccode = $(this).attr("doccode")
                    val = $(this).val();
                    let obj = {
                        SourceId: val,
                        RefCode: vm.doccode
                    }
                    vm.moveFolderObj.push(obj);
                });
            }
            else {
                let obj = {
                    SourceId: val,
                    RefCode: vm.doccode
                }
                vm.moveFolderObj.push(obj);
            }


            var jsonstr = JSON.stringify(vm.moveFolderObj);
            var params = `jsonData=${jsonstr}&destFolderid=${vm.folderId}`;
            DataService.CheckMovedFolderIdEqualToDestFolderId(params).then((response) => {
                if (response.data == 5) {
                    vm.confirmR("The destination folder is a subfolder of the source folder.", true, (vm.$t("Delete") + "  " + vm.$t("Groups")), function () {
                        DataService.MoveFileFolder(params).then((response) => {
                            
                            if (response.data == 1) {
                                vm.ShowAlert(vm.$t("MoveFolderFileSucces"), "success", true, vm.$t("Alert"));
                                vm.moveFolderObj = [];
                                vm.folderFileId = "";
                                vm.GetFullTree();
                                vm.StorageDocumentListing();
                                vm.closepopupFolder();
                            }
                            else if (response.data == 5) {
                                vm.ShowAlert(vm.$t("SeletedfolderSameAsDestinationFolder"), "warning", true, vm.$t("Alert"));
                                vm.moveFolderObj = [];
                            }
                            else if (response.data == -2) {
                                vm.ShowAlert(vm.$t("MoveFolderFileExists"), "failure", true, vm.$t("Alert"));
                                vm.moveFolderObj = [];
                                vm.StorageDocumentListing();
                                vm.closepopupFolder();
                            }
                            else {
                                vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                            }
                        })
                    })
                }
                else {
                    DataService.MoveFileFolder(params).then((response) => {
                        
                        if (response.data == 1) {
                            vm.ShowAlert(vm.$t("MoveFolderFileSucces"), "success", true, vm.$t("Alert"));
                            vm.moveFolderObj = [];
                            vm.folderFileId = "";
                            vm.GetFullTree();
                            vm.StorageDocumentListing();
                            vm.closepopupFolder();
                        }
                        else if (response.data == -2) {
                            vm.ShowAlert(vm.$t("MoveFolderFileExists"), "failure", true, vm.$t("Alert"));
                            vm.moveFolderObj = [];
                            vm.StorageDocumentListing();
                            vm.closepopupFolder();
                        }
                        else {
                            vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                        }
                    })
                }
            })

        },

        ShareDoc: function (Id, doccode,name) {
            var vm = this;
            vm.generateJSONData=[];
            $('#tblFileList .chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                vm.IsDocCode = $(this).attr("doccode");
                vm.sourceIds = $(this).attr("value");
                vm.Name=$(this).closest('tr').find('.aFileDownload').html();
                let obj = {
                    SourceId: vm.sourceIds,
                    RefCode: vm.IsDocCode,
                    Name:vm.Name,
                    filePath:$(this).attr("filePath")
                }
                vm.generateJSONData.push(obj);
            })
            if(Id!=null || Id>0){
                let objs = {
                    SourceId: Id,
                    RefCode: doccode,
                    Name:name,
                }
                vm.generateJSONData.push(objs);
            }
            vm.sharemodal = true;
            vm.idFile = Id;
            vm.IsDocCode = doccode;

        },
        GetFileDetails: function (id, doccode, filename, folderId, renamid) {
            var vm = this;
            vm.docCode = doccode;
            vm.fileId = id;
            vm.fileName = filename.split('.')[0];
            var params = `fileId=${id}&folderId=${folderId}&refCode=&fileName=${vm.fileName}`;
            if (renamid != 0) {
                DataService.GetFileDetail(params).then((response) => {
                    if (response.data == "2") {
                        vm.ShowAlert(vm.$t('FileRenameSuccess'))
                        vm.StorageDocumentListing(folderId);
                        vm.closepopup();
                    }
                    else {
                        vm.ShowAlert(vm.$t('File_Already_Exists'))
                    }
                });
            }

        },
        GetRecentlyAddPinnedFolder: function () {
            var vm = this;
            vm.pinnedFolderArray=[];
            vm.RecentFolderArray=[];
            DataService.GetRecentlyAndPinnedFolderTreeData().then((response) => {
                vm.recentFolderList = JSON.parse(response.data).RecentFolderList
                vm.pinnedFolderList = JSON.parse(response.data).PinnedFolderList
                vm.recentFolderList.forEach((value, index) => {
                    if (value.OWNER_USER_ID == vm.loggeduserid) {
                        var CheckIfExist = vm.RecentFolderArray.filter(function (el) {
                            return el.FOLDER_ID == value.FOLDER_ID;
                        });


                        if (CheckIfExist.length == 0) {
                            if (value.FOLDER_PARENT_ID == null) {
                                let folderId = value.FOLDER_ID
                                let rslt = vm.getsharedChildFolder(folderId, value, vm.folderTreeList);
                                vm.RecentFolderArray.push(rslt)
                            }
                        }

                    }

                });
                vm.pinnedFolderList.forEach((value, index) => {
                    if (value.OWNER_USER_ID == vm.loggeduserid) {
                        var CheckIfExist = vm.pinnedFolderArray.filter(function (el) {
                            return el.FOLDER_ID == value.FOLDER_ID;
                        });


                        if (CheckIfExist.length == 0) {
                            // if (value.FOLDER_PARENT_ID == null) {
                                let folderId = value.FOLDER_ID
                                let rslt = vm.getsharedChildFolder(folderId, value, vm.folderTreeList);
                                vm.pinnedFolderArray.push(rslt)
                            //}
                        }

                    }

                });
            });

        },
        GetOrganizationSpace: function () {

            var vm = this;
            vm.organizationSpace=[]
            DataService.GetOrganizationFolderTreeData().then((response) => {
                response.data.folderTreeList.forEach((value, index) => {
                    var CheckIfExist = vm.organizationSpace.filter(function (el) {
                        return el.FOLDER_ID == value.FOLDER_ID;
                    });


                    if (CheckIfExist.length == 0) {
                        if (value.FOLDER_PARENT_ID == null) {
                            let folderId = value.FOLDER_ID
                            let rslt = vm.getsharedChildFolder(folderId, value, vm.folderTreeList);
                            vm.organizationSpace.push(rslt)
                        }
                    }
                });
            });
            

        },
        OpenTagPopUp: function () {
            
            var vm = this;
            vm.isDivActiveFromTagListing=false;
            vm.isDivActive=true;
            vm.saveTagPopupShow = true;
        },
        closeAddTagPopup: function () {
            var vm = this;
            vm.saveTagPopupShow = false;
        }
    }
}
</script>
