<template>
    
    <div class="col-md-12 pb-2 px-0" id="dsfileList">
        <loader :is-visible="isLoading"></loader>
        <div class="theme-primary partition p-actions-expand tablehead p-actions-collapse mb-3">
            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left text-right p-0 theme-primary">
                <span class="p-actions float-right">
                    <a href="javascript:;" v-bind:class="{ 'p-action-btn text-white': true, 'disabled': false }"
                        title="CardView" data-toggle-tooltip="tooltip" data-original-title="Card View"
                        v-on:click="listType = 'CardView';"
                        v-if="listType == 'ListView'">
                        <em class="fa fa-th-large"></em>
                        <br />
                        {{ $t('CardView') }}
                    </a>
                    <a href="javascript:;" v-bind:class="{ 'p-action-btn text-white': true, 'disabled': false }"
                        title="List View" data-toggle-tooltip="tooltip" data-original-title="List View"
                        v-on:click="listType = 'ListView';"
                        v-if="listType == 'CardView'">
                        <em class="fa fa-list"></em> <br />{{ $t('ListView') }}
                    </a>

                    <a href="javascript:;" title="Pin" id="pinUnpin"
                        v-if="commonParentName != 'Recently Added' && commonParentName != 'Pinned Folders'"
                        class="p-action-btn text-white btnpin iconenable enable disabled" v-on:click="MarkAsPin">
                        <em aria-hidden="true" class="fa fa-thumb-tack"></em> <br>{{ $t('PIN') }}
                    </a>

                    <a id="assignTag" href="javascript:;" @click="OpenAddTag" class="p-action-btn text-white disabled"
                        title="Assign Tag" data-toggle-tooltip="tooltip" data-original-title="Add"
                        v-if="commonParentName != 'Recently Added' && commonParentName != 'Pinned Folders'">
                        <em aria-hidden="true" class="fa fa-share"></em> <br>{{ $t('AssingTag') }}
                    </a>
                    <a id="aAddFolder" href="javascript:;" v-if="commonParentName != 'Recently Added' && commonParentName != 'Pinned Folders'"
                        :class="{ 'disabled': folderPermission == 'r' || commonParentName == 'Shared Folders' }" @click="openpopup"
                        class="p-action-btn text-white" title="Add Folder" data-toggle-tooltip="tooltip"
                        data-original-title="Add">
                        <em :class="{ 'disabled': folderPermission == 'r' || commonParentName == 'Shared Folders' }"
                            class="fa fa-folder text-white"></em> <br>{{ $t('AddFolder') }}
                    </a>
                    <a id="moveFolder" href="javascript:;" @click="MoveFoldersFiles"
                        v-if="commonParentName != 'Recently Added' && commonParentName != 'Pinned Folders'"
                        class="p-action-btn text-white disabled" title="Move" data-toggle-tooltip="tooltip"
                        data-original-title="Add">
                        <em class="fa fa-arrows  action-icon"></em> <br>{{ $t("Move") }}
                    </a>
                    <a href="javascript:;" id="aShareAll" title="Share" class="p-action-btn text-white disabled"
                        @click="popup" v-if="commonParentName != 'Recently Added' && commonParentName != 'Pinned Folders'">
                        <em class="fa fa-share-alt"></em> <br />{{ $t('Share') }}
                    </a>
                    <a href="javascript:;" id="aAddTag" @click="OpenTagPopUp" title="AddTag"
                        v-if="userType == 'CA' && commonParentName != 'Recently Added' && commonParentName != 'Pinned Folders'"
                        class="p-action-btn text-white">
                        <em class="fa fa-tag"></em> <br />{{ $t('AddTag') }}
                    </a>
                    <a href="javascript:;" id="aDeleteAll" @click="CommonDelete" title="Delete" v-if="commonParentName != 'Recently Added' && commonParentName != 'Pinned Folders'"
                        class="p-action-btn text-white disabled">
                        <em class="fa fa-trash"></em> <br />{{ $t('Delete') }}
                    </a>

                </span>
                <span class="collapse-head-btns">
                    <a href="javascript:;" class="collapse-btns"><em class="fa fa-angle-up text-white"></em> </a>
                </span>

            </div>

        </div>
        <div class="col-12 p-0 mt-0">
          
            <div class="col-md-12 p-0 float-left">

                <div class="heading-border">
                    <h5 class="h5">
                        <span class="font-weight-medium mr-2">{{ $t('YouAreInside') }}</span>

                        <span class="text-primary border-0 mr-2 font-16">
                            <a @click="SpecificFolder('', commonParentName)" class="text-primary">{{
                                commonParentName
                            }}</a></span>
                        <span class=" border-0 font-16" v-for="(item, index) in parentname" :key="index"
                            :class="{ 'disabled': parentname.length - 1 == index }"><a v-if="parentname[0] != ''"
                                :class="{ 'disabled text-disabled': parentname.length - 1 == index, 'text-primary': parentname.length - 1 != index }"
                                @click="SpecificFolder(item[1], commonParentName)" :folderId="item[1]"> > {{
                                    item[0] }}</a></span>
                        <small class="float-right">
                            <a href="javascript:void(0)"
                                v-if="commonParentName != 'Recently Added' && commonParentName != 'Pinned Folders' && commonParentName != 'Shared Folders'"
                                :class="{ 'disabled': folderPermission == 'r' || commonParentName == 'Shared' }" title=""
                                id="UploadFilesFolder" @click="ShowUpload(isShowUploadDiv)" data-toggle-tooltip="tooltip"
                                data-original-title="Add User">
                                <i class="fa fa-upload pr-1"></i>Upload Files/Folder
                            </a> </small>
                    </h5>
                </div>
            </div>

            <file-upload :folderId="folderId" v-if="isShowUploadDiv == true" :isGlobal="0"
                @update-list-after-upload="updatelistafterupload" :listing="listingdata"></file-upload>

        </div>

        <div class="row">
            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 mb-2 searchpanel tablehead search float-left">
                <div class="w-100 ">
                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 Search-panel float-left  pt-2 p-0">

                        <div class="col-sm-12 col-md-12 col-lg-6 col-xl-6 float-left">
                            <div class="row mt-0">
                                <div class="col-7 float-left pr-1 pl-0">
                                    <div class="form-group">
                                        <input id="search" maxlength="50" @keydown.enter="SearchData(commonParentName)"
                                            v-model="searchCondition" class="form-control search" type="text"
                                            placeholder="Search by Folder name, File name, Tag ..." autocomplete="off">
                                    </div>
                                </div>
                                <div class="col-5 float-left pl-0">
                                    <div class="background-0 text-left form-check-inline">
                                        <input type="hidden" id="hdnSearch" value="">
                                        <a title="" id="Go" @click="SearchData(commonParentName)" href="javascript:;"
                                            class="btn btn-success mr-1 px-4 hlsearch" data-toggle-tooltip="tooltip"
                                            data-original-title="Search" aria-describedby="tooltip697211"><i
                                                class="fa fa-search"></i></a>
                                        <a href="javascript:;" title="" @click="RefreshPage"
                                            class="btn btn-danger clearsearchtext px-4" data-toggle-tooltip="tooltip"
                                            data-original-title="Clear all "><i class="fa fa-refresh"></i></a>
                                    </div>
                                </div>

                            </div>
                        </div>

                    </div>
                </div>
            </div>
            <div class="clearfix"></div>
        </div>

        <div class="listing">
            <div class="expand-list">
                <a href="javascript:;" @click="ExpandCollapseList" class="expand-list-btn justify-content-center align-items-center">
                    <em class="icon-solid arrow-solid-left text-white" style="font-size:9pt"></em>
                </a>
            </div>
            <div v-if="listType == 'ListView'" class="table-responsive table-fix-header" id="div_forListView">
                <table class="table table-bordered mt-0" v-if="isShowMySpaceListing == true" style="width: 100%;"
                    id="tblFileList">
                    <thead class="thead-bg">
                        <tr>
                            <th style="width:50px !important;" v-on:change="onChange()" class="text-center" v-if="commonParentName=='My Space'"><input 
                                    id="chkAll" class="chkItems" value="" type="checkbox"></th>
                            <th class="ui-resizable" @click="SortData($event, 'Name', 'Sort', commonParentName)">
                                <span class="sort"
                                    :class="{ 'tb_headerSortDown': sortExp == 'ASC' && specificSortClick == 'Name', 'tb_headerSortUp': sortExp == 'DESC' && specificSortClick == 'Name' }">Name</span>
                            </th>
                            <th class="text-center" @click="SortData($event, 'Tags', 'Sort', commonParentName)">
                                <span class="sort"
                                    :class="{ 'tb_headerSortDown': sortExp == 'ASC' && specificSortClick == 'Tags', 'tb_headerSortUp': sortExp == 'DESC' && specificSortClick == 'Tags' }">Tags</span>
                            </th>
                            <th class="text-center" @click="SortData($event, 'CreatedBy', 'Sort', commonParentName)">
                                <span class="sort"
                                    :class="{ 'tb_headerSortDown': sortExp == 'ASC' && specificSortClick == 'CreatedBy', 'tb_headerSortUp': sortExp == 'DESC' && specificSortClick == 'CreatedBy' }">Created
                                    By</span>
                            </th>
                            <th class="text-center">
                                <span
                                   >Type
                                    </span>
                            </th>
                            <th class="text-center" @click="SortData($event, 'DocSize', 'Sort', commonParentName)">
                                <span class="sort"
                                    :class="{ 'tb_headerSortDown': sortExp == 'ASC' && specificSortClick == 'DocSize', 'tb_headerSortUp': sortExp == 'DESC' && specificSortClick == 'DocSize' }">Size</span>
                            </th>
                            <th class="text-center">
                                <span>{{ $t('CreatedOn') }}</span>
                            </th>
                            <th data-width="5%" style="width:100px;" class="text-center nonresize">
                                <span>{{ $t('Action') }}</span>
                            </th>
                        </tr>
                    </thead>
                    <tbody>

                         <tr v-for="(item,index1) in listingdata" :key="index1">
                            <td class="text-center" v-if="commonParentName=='My Space'"><input :value="item.Id" :filePath="GetThumbnail(item.DocPathLocation)" 
                                    :folderId="item.ParentId" :data-tagIds="item.TagIds" v-on:change="onChange()"
                                    :docCode="item.IsDocCode" v-bind:fileid="item.FileId" :PinId="item.PinId"
                                     class="chkItems" type="checkbox"></td>
                            <td :class="(item.IsDocCode == 1 ? 'file-image doc-icon' : '')">
                                <div class="pin pr-2 d-inline-block">
                                    <em style="cursor:pointer;" :pinId="item.PinId" class="fa fa-thumb-tack"
                                        @click="MarkAsUnPin(item.Id)" v-if="item.PinId > 0" aria-hidden="true"></em>
                                </div>
                                <em class="fa fa-folder pr-2 font-28 text-primary" style="font-size:25px"
                                    v-if="item.IsDocCode == 0" @click="StorageDocumentListing(item.Id, commonParentName, '', 'a' + item.Id)" aria-hidden="true"></em>
                                <span class="text-overflow-dynamic-container" v-if="item.IsDocCode == 0">
                                    {{ item.pin_id }}
                                    <a class="aFileDownload" :value="item.Id"
                                        @click="StorageDocumentListing(item.Id, commonParentName, '', 'a' + item.Id)"
                                        v-bind:folderId="item.Id" v-bind:filepath="item.FilePath"
                                        v-bind:filetype="item.FileType" v-bind:filename="item.FileName" href="javascript:;"
                                        title="">{{ item.NAME }}</a>
                                 
                                </span>
                                <img style="height: 30px;"
                                    v-if="item.IsDocCode == 1 && (item.DocExtention == 'pptx' || item.DocExtention == 'docx' || item.DocExtention == 'pdf' || item.DocExtention == 'jpg' || item.DocExtention == 'png' || item.DocExtention == 'jpeg' || item.DocExtention == 'xlsx')"
                                     :alt='item.FileType' :title='item.DocType'
                                    :data-fileType='item.DocExtention' :src="GetThumbnail(item.FileThumbnailPath)"
                                    @click="FilesDetail(item.Id, item.IsDocCode, item.DocPathLocation, item.DocExtention, item.ParentId, item.PERMISSION)" />
                                <em v-if="item.IsDocCode == 1" 
                                    :class="GetFileIconClass(item.DocExtention)" width="27px"></em> &nbsp;
                                <span class="text-overflow-dynamic-container" v-if="item.IsDocCode == 1">
                                    <RouterLink v-if="item.IsDocCode == 1" class="aFileDownload" v-bind:id="item.Id" :title="item.NAME"
                                        :href="'/ViewFileDetails/' + item.Id + '/' + item.IsDocCode + '/' + item.ParentId + '/' + item.DocExtention + '/' + null + '/' + commonParentName + '/' + refCode"
                                        :to="'/ViewFileDetails/' + item.Id + '/' + item.IsDocCode + '/' + item.ParentId + '/' + item.DocExtention + '/' + null + '/' + commonParentName + '/' + refCode">
                                        {{ item.NAME }}

                                    </RouterLink>
                                </span>
                            </td>
                            <td class="text-center">
                                <span class="text-center text-overflow-dynamic-container">
                                    {{ item.Tags }}
                                </span>
                            </td>
                            <td class="file-image doc-icon text-center">
                                <span class="text-center">
                                    {{ item.CreatedBy }}
                                </span>
                            </td>
                             <td class="file-image doc-icon text-center">
                                <span class="text-center">
                                  <em :class="GetFileIcon(item.DocExtention)"></em>
                                </span>
                            </td>
                            <td class="text-center">
                                <span class="text-center text-overflow-dynamic-container">
                                    {{ convertmbtoKb(item.DocSize) }}
                                </span>
                            </td>
                            <td class="text-center">
                                <span class="text-center text-overflow-dynamic-container">
                                    {{ $options.filters.formatDate(item.created_at) }}
                                </span>
                            </td>
                            <td class="text-center mobile-action">
                                <span class="actions mobileaction rt_25">
                                    <em class="fa fa-ellipsis-h action_icon" aria-hidden="true"></em>
                                    <span class="action-list-box">
                                        <span class="list-actions w-90" id="action-list">
                                            <RouterLink v-if="item.IsDocCode == 0" class="aFileDownload actions-onclick"
                                                v-bind:id="item.Id" 
                                                :href="'/ViewFolderDetails/' + item.Id + '/' + item.IsDocCode + '/' + item.ParentId + '/' + null + '/' + commonParentName + '/' + refCode"
                                                :to="'/ViewFolderDetails/' + item.Id + '/' + item.IsDocCode + '/' + item.ParentId + '/' + null + '/' + commonParentName + '/' + refCode">
                                                <em class="fa fa-eye action-icon"></em>
                                                <span class="text">View</span>
                                            </RouterLink>
                                            <RouterLink v-if="item.IsDocCode == 1" class="aFileDownload actions-onclick"
                                                v-bind:id="item.Id"
                                                :href="'/ViewFileDetails/' + item.Id + '/' + item.IsDocCode + '/' + item.ParentId + '/' + item.DocExtention + '/' + null + '/' + commonParentName + '/' + refCode"
                                                :to="'/ViewFileDetails/' + item.Id + '/' + item.IsDocCode + '/' + item.ParentId + '/' + item.DocExtention + '/' + null + '/' + commonParentName + '/' + refCode">
                                                <em class="fa fa-eye action-icon"></em>
                                                <span class="text">View</span>
                                            </RouterLink>
                                            <a class="aFileDownload actions-onclick" v-if="item.IsDocCode == 1"
                                                v-bind:id="item.Id" href="javascript:;"
                                                v-on:click="DownloadFiles(item.Id, item.NAME)">
                                                <em class="fa fa-download text-primary action-icon"></em>
                                                <span class="text">Download</span>
                                            </a>
                                            <a class="aFileShare actions-onclick" @click="popup(item.Id, item.IsDocCode, item.NAME)"
                                                href="javascript:;">
                                                <em class="fa fa-share-alt text-primary action-icon"></em>
                                                <span class="text">Share</span>
                                            </a>
                                            <a href="javascript:;" v-if="item.IsDocCode == 0"
                                                @click="GetFolderDetail(item.Id, item.IsDocCode, item.ParentId)"
                                                class="aFileRename actions-onclick">
                                                <em class="fa fa-pencil text-success action-icon"></em>
                                                <span class="text">Rename</span>
                                            </a>
                                            <a href="javascript:;" v-if="item.IsDocCode == 1"
                                                @click="GetFileDetail(item.Id, item.IsDocCode, item.NAME, 0)"
                                                class="aFileRename actions-onclick">
                                                <em class="fa fa-pencil text-success action-icon"></em>
                                                <span class="text">Rename</span>
                                            </a>
                                            <a href="javascript:;" @click="MoveFoldersFiles(item.Id, item.IsDocCode)"
                                                v-bind:id="item.Id" class="aFileMove actions-onclick">
                                                <em class="fa fa-arrows text-warning action-icon"></em>
                                                <span class="text">Move</span>
                                            </a>
                                            <a href="javascript:;" v-if="item.IsDocCode == 0" v-bind:id="item.Id"
                                                @click="CommonDelete(item.Id, item.IsDocCode)"
                                                class="aFileDelete actions-onclick">
                                                <em class="fa fa-trash text-danger action-icon"></em>
                                                <span class="text">Delete</span>
                                            </a>
                                            <a href="javascript:;" v-if="item.IsDocCode == 1" v-bind:id="item.Id"
                                                @click="CommonDelete(item.Id, item.IsDocCode)"
                                                class="aFileDelete actions-onclick">
                                                <em class="fa fa-trash text-danger action-icon"></em>
                                                <span class="text">Delete</span>
                                            </a>
                                            <em class="fa fa-times close close-action" aria-hidden="true"></em>
                                        </span>
                                    </span>
                                </span>
                            </td>
                        </tr>
                        <tr>
                            <td v-if="listingdata.length == 0" colspan="7" class="text-danger text-center no-record">{{ $t("NoRecordfound") }}</td>
                        </tr>
                    </tbody>
                </table>
                <table class="table table-bordered mt-0" v-if="isShowSharedListing == true" style="width: 100%;"
                    id="tblFileList">
                    <thead class="thead-bg">
                        <tr>
                            <th style="width:50px !important;" v-on:change="onChange()" class="text-center"><input
                                    id="chkAll" class="chkItems" value="" type="checkbox"></th>
                            <th class="ui-resizable" @click="SortData($event, 'Name', 'Sort', commonParentName)">
                                <span class="sort" :class="{ 'tb_headerSortDown': sortExp == 'ASC' && specificSortClick == 'Name', 'tb_headerSortUp': sortExp == 'DESC' && specificSortClick == 'Name' }">{{$t("Names")}}</span>
                            </th>
                            <th class="ui-resizable text-center" @click="SortData($event, 'Tags', 'Sort', commonParentName)">
                                <span class="sort" :class="{ 'tb_headerSortDown': sortExp == 'ASC' && specificSortClick == 'Tags', 'tb_headerSortUp': sortExp == 'DESC' && specificSortClick == 'Tags' }">Tags</span>
                            </th>
                            <th class="ui-resizable text-center" @click="SortData($event, 'CreatedBy', 'Sort', commonParentName)">
                                <span class="sort" :class="{ 'tb_headerSortDown': sortExp == 'ASC' && specificSortClick == 'CreatedBy', 'tb_headerSortUp': sortExp == 'DESC' && specificSortClick == 'CreatedBy' }">{{$t("CreatedBy")}}</span>
                            </th>
                            <th class="text-center">
                                <span
                                   >Type
                                    </span>
                            </th>
                            <th class="ui-resizable text-center" @click="SortData($event, 'DocSize', 'Sort', commonParentName)">
                                <span class="sort"  :class="{ 'tb_headerSortDown': sortExp == 'ASC' && specificSortClick == 'DocSize', 'tb_headerSortUp': sortExp == 'DESC' && specificSortClick == 'DocSize' }">{{$t("Size")}}</span>
                            </th>
                            <th class="text-center">
                                <span>{{$t("CreatedOn")}}</span>
                            </th>
                            <th data-width="5%" class="text-center nonresize">
                                <span>{{$t("Action")}}</span>
                            </th>
                        </tr>
                    </thead>
                    <tbody>

                        <tr v-for="(item,itemIndex) in listingdata" :key="itemIndex">
                            <td class="text-center"><input :value="item.Id" v-on:change="onChange()"
                                    :docCode="item.IsDocCode" v-bind:fileid="item.FileId" :PinId="item.PinId==null?0:item.PinId"
                                    :disabled="item.PERMISSION == 'w' || item.PERMISSION == 'r' || (docPermission == 'w' && item.PERMISSION == null) || (docPermission == 'r' && item.PERMISSION == null)"
                                    v-bind:filepath="item.FilePath" class="chkItems" type="checkbox"></td>
                            <td class="file-image doc-icon">
                                <div class="pin pr-2 d-inline-block">
                                    <em class="fa fa-thumb-tack" @click="MarkAsUnPin(item.Id)" v-if="item.PinId > 0"
                                        aria-hidden="true"></em>
                                </div>
                                <em class="fa fa-folder pr-2 font-28 text-primary" v-if="item.IsDocCode == 0"
                                    aria-hidden="true"></em>
                                <span class="text-overflow-dynamic-container" v-if="item.IsDocCode == 0">
                                    {{ item.pin_id }}
                                    <a class="aFileDownload" :value="item.Id"
                                        @click="StorageDocumentListing(item.Id, commonParentName, item.PERMISSION == undefined ? folderPermission : item.PERMISSION, item.ParentId)"
                                        v-bind:folderId="item.Id" v-bind:parentId="item.ParentId"
                                        v-bind:filepath="item.FilePath" v-bind:filetype="item.FileType"
                                        v-bind:filename="item.FileName" href="javascript:;" title="">{{ item.NAME }}</a>
                                    <!-- <em class="fa fa-thumb-tack" @click="MarkAsUnPin(item.Id)" v-if="item.PinId > 0" aria-hidden="true"></em>  -->
                                </span>
                                <img style="height: 30px;"
                                    v-if="item.IsDocCode == 1 && (item.DocExtention == 'pptx' || item.DocExtention == 'docx' || item.DocExtention == 'pdf' || item.DocExtention == 'jpg' || item.DocExtention == 'png' || item.DocExtention == 'jpeg' || item.DocExtention == 'xlsx')"
                                     :alt='item.FileType' :title='item.DocType'
                                    :data-fileType='item.DocExtention' :src="GetThumbnail(item.FileThumbnailPath)"
                                    @click="FilesDetail(item.Id, item.IsDocCode, item.DocPathLocation, item.DocExtention, item.ParentId, item.PERMISSION)" />

                                <em v-if="item.IsDocCode == 1" 
                                    :class="GetFileIconClass(item.DocExtention)" width="27px"></em> &nbsp;
                                <span class="text-overflow-dynamic-container" v-if="item.IsDocCode == 1">
                                   
                                    <RouterLink v-if="item.IsDocCode == 1" class="aFileDownload" v-bind:id="item.Id"
                                        :href="'/ViewFileDetails/' + item.Id + '/' + item.IsDocCode + '/' + item.ParentId + '/' + item.DocExtention + '/' + (item.PERMISSION == undefined ? folderPermission : item.PERMISSION) + '/' + commonParentName + '/' + refCode"
                                        :to="'/ViewFileDetails/' + item.Id + '/' + item.IsDocCode + '/' + item.ParentId + '/' + item.DocExtention + '/' + item.PERMISSION + '/' + commonParentName + '/' + refCode">
                                        {{ item.NAME }}
                                    </RouterLink>

                                </span>
                            </td>
                            <td class="file-image doc-icon text-center">
                                <span class="text-center">
                                    {{ item.Tags }}
                                </span>
                            </td>
                            <td class="file-image doc-icon text-center">
                                <span class="text-center">
                                    {{ item.CreatedBy }}
                                </span>
                            </td>
                              <td class="file-image doc-icon text-center">
                                <span class="text-center">
                                  <em :class="GetFileIcon(item.DocExtention)"></em>
                                </span>
                            </td>
                            <td class="file-image doc-icon text-center">
                                <span class="text-center ">
                                    {{ convertmbtoKb(item.DocSize) }}
                                </span>
                            </td>
                            <td class="file-image doc-icon text-center">
                                <span class="text-center">
                                    {{ $options.filters.formatDate(item.created_at) }}
                                </span>
                            </td>
                            <td class="text-center mobile-action rt_25"
                                v-if="item.PERMISSION != 'r' || item.PERMISSION !== null || docPermission != 'r'">
                                <span class="text-center actions mobileaction">
                                    <em class="fa fa-ellipsis-h action_icon" aria-hidden="true"></em>
                                    <span class="action-list-box">
                                        <span class="list-actions" id="action-list">
                                            <RouterLink v-if="item.IsDocCode == 0" class="aFileDownload actions-onclick"
                                                v-bind:id="item.Id"
                                                :href="'/ViewFolderDetails/' + item.Id + '/' + item.IsDocCode + '/' + item.ParentId + '/' + (item.PERMISSION == undefined ? folderPermission : item.PERMISSION) + '/' + commonParentName + '/' + refCode"
                                                :to="'/ViewFolderDetails/' + item.Id + '/' + item.IsDocCode + '/' + item.ParentId + '/' + item.PERMISSION + '/' + commonParentName + '/' + refCode">
                                                <em class="fa fa-eye action-icon"></em>
                                                <span class="text">View</span>
                                            </RouterLink>
                                            <RouterLink v-if="item.IsDocCode == 1" class="aFileDownload actions-onclick"
                                                v-bind:id="item.Id"
                                                :href="'/ViewFileDetails/' + item.Id + '/' + item.IsDocCode + '/' + item.ParentId + '/' + item.DocExtention + '/' + (item.PERMISSION == undefined ? folderPermission : item.PERMISSION)  + '/' + commonParentName + '/' + refCode"
                                                :to="'/ViewFileDetails/' + item.Id + '/' + item.IsDocCode + '/' + item.ParentId + '/' + item.DocExtention + '/' + (item.PERMISSION == undefined ? folderPermission : item.PERMISSION)  + '/' + commonParentName + '/' + refCode">
                                                <em class="fa fa-eye action-icon"></em>
                                                <span class="text">View</span>
                                            </RouterLink>
                                            <a class="aFileDownload actions-onclick"  v-on:click="DownloadFiles(item.Id, item.NAME)"
                                                v-if="item.IsDocCode == 1 && (item.PERMISSION == 'w' || item.PERMISSION == 'o')"
                                                v-bind:id="item.Id" href="javascript:;">
                                                <em class="fa fa-download text-primary action-icon"></em>
                                                <span class="text">{{ $t('Download') }}</span>
                                            </a>
