                                                <span class="text">{{ $t('Delete') }}</span>
                                            </a>
                                            <a href="javascript:;" v-if="item.IsDocCode == 1 && item.PERMISSION == 'o'"
                                                v-bind:id="item.Id" @click="CommonDelete(item.Id, item.IsDocCode)"
                                                class="aFileDelete actions-onclick">
