            </div>
            <div v-if="listType == 'CardView' && listingdata.length>0" class="row card-view bg-border custom-box-width mt-2" id="div_forCardView">
                <div v-for="(file,fileIndex) in listingdata" :key="fileIndex"
                    class='col-sm-12 col-lg-2 col-xl-2 col-md-12 mb-3 div-cardView'>
                    <div class='box docattachment border'>
                        <div class="verticle-action">
