                                            :to="'/ViewFileDetails/' + file.Id + '/' + file.IsDocCode + '/' + file.ParentId + '/' + file.DocExtention + '/' + (file.PERMISSION == undefined ? folderPermission : file.PERMISSION)  + '/' + commonParentName + '/' + refCode">
