                                        <a href="javascript:;"
                                            v-if="file.IsDocCode == 1 && (isShowSharedListing == true && (file.PERMISSION == 'o' || file.PERMISSION == 'w') || isShowSharedListing == false)"
                                            @click="GetFileDetail(file.Id, file.IsDocCode, file.NAME, 0)"
                                            class="aFileRename actions-onclick">
                                            <em class="fa fa-pencil text-success action-icon"></em>
                                            <span class="text">Rename</span>
                                        </a>
                                        <a href="javascript:;"
                                            v-if="isShowSharedListing == true && (file.PERMISSION == 'o') || isShowSharedListing == false"
