                        <input :value="file.Id" :folderId="file.ParentId" :data-tagIds="file.TagIds"
                            v-on:change="onChange()" :docCode="file.IsDocCode" :PinId="file.PinId" class="chkItems"
                            :disabled="file.PERMISSION == 'w' || folderPermission == 'w' || folderPermission == 'r' || file.PERMISSION == 'r' || (docPermission == 'w' && file.PERMISSION == null) || (docPermission == 'r' && file.PERMISSION == null)"
                            type="checkbox"
                            v-if="(commonParentName=='My Space' || (file.PERMISSION == 'w' || folderPermission == 'w' || folderPermission == 'r' || file.PERMISSION == 'r' || (docPermission == 'w' && file.PERMISSION == null) || (docPermission == 'r' && file.PERMISSION == null)))">
                      
                        <div class="pinff pinnewtag pr-2 d-inline-block">
                            <em class="fa fa-thumb-tack" @click="MarkAsUnPin(file.Id)" v-if="file.PinId > 0"
                                aria-hidden="true"></em>
                        </div>

                        <div class='img-icons new text-center p-2 file-image doc-icon'>
                           
                            <RouterLink v-if="file.IsDocCode == 1 && isShowSharedListing == true" class="aFileDownload" v-bind:id="file.Id"
                                :href="'/ViewFileDetails/' + file.Id + '/' + file.IsDocCode + '/' + file.ParentId + '/' + file.DocExtention + '/' + (file.PERMISSION == undefined ? folderPermission : file.PERMISSION) + '/' + commonParentName + '/' + refCode"
                                :to="'/ViewFileDetails/' + file.Id + '/' + file.IsDocCode + '/' + file.ParentId + '/' + file.DocExtention + '/' + (file.PERMISSION == undefined ? folderPermission : file.PERMISSION) + '/' + commonParentName + '/' + refCode">
                                <img 
                                    v-if="file.DocExtention == 'docx' || file.DocExtention == 'pptx'|| file.DocExtention == 'pdf' || file.DocExtention == 'jpg' || file.DocExtention == 'png' || file.DocExtention == 'jpeg' || file.DocExtention == 'xlsx'"
                                    :alt='file.FileType' :title='file.Name'
                                    :data-fileType='file.DocExtention' :src="GetThumbnail(file.FileThumbnailPath)" />

                            </RouterLink>
                            <RouterLink v-if="file.IsDocCode == 1 && isShowSharedListing == false" class="aFileDownload" v-bind:id="file.Id"
                                :href="'/ViewFileDetails/' + file.Id + '/' + file.IsDocCode + '/' + file.ParentId + '/' + file.DocExtention + '/' +null+ '/' + commonParentName + '/' + refCode"
                                :to="'/ViewFileDetails/' + file.Id + '/' + file.IsDocCode + '/' + file.ParentId + '/' + file.DocExtention + '/' +null + '/' + commonParentName + '/' + refCode">
                                <img  v-if="file.DocExtention == 'docx' || file.DocExtention == 'pptx'|| file.DocExtention == 'pdf' || file.DocExtention == 'jpg' || file.DocExtention == 'png' || file.DocExtention == 'jpeg' || file.DocExtention == 'xlsx'"
                                    :alt='file.FileType' :title='file.Name'
                                    :data-fileType='file.DocExtention' :src="GetThumbnail(file.FileThumbnailPath)" />

                            </RouterLink>
                            <RouterLink v-if="file.IsDocCode == 1 && isShowSharedListing == true" class="aFileDownload" v-bind:id="file.Id"
                                :href="'/ViewFileDetails/' + file.Id + '/' + file.IsDocCode + '/' + file.ParentId + '/' + file.DocExtention + '/' + (file.PERMISSION == undefined ? folderPermission : file.PERMISSION) + '/' + commonParentName + '/' + refCode"
                                :to="'/ViewFileDetails/' + file.Id + '/' + file.IsDocCode + '/' + file.ParentId + '/' + file.DocExtention + '/' + (file.PERMISSION == undefined ? folderPermission : file.PERMISSION) + '/' + commonParentName + '/' + refCode">
                                <em
                                    v-if="file.IsDocCode == 1"
                                    :class="GetFileIconClass(file.DocExtention)" class="pt-3" :alt='file.FileType' :title='file.Name'
                                    :data-fileType='file.DocExtention'
                                    @click="FilesDetail(file.Id, file.IsDocCode, file.DocPathLocation, file.DocExtention, file.ParentId, file.PERMISSION)"></em>
                            </RouterLink>
                            <RouterLink v-if="file.IsDocCode == 1 && isShowSharedListing == false" class="aFileDownload" v-bind:id="file.Id"
                                :href="'/ViewFileDetails/' + file.Id + '/' + file.IsDocCode + '/' + file.ParentId + '/' + file.DocExtention + '/' +null + '/' + commonParentName + '/' + refCode"
                                :to="'/ViewFileDetails/' + file.Id + '/' + file.IsDocCode + '/' + file.ParentId + '/' + file.DocExtention + '/' + null + '/' + commonParentName + '/' + refCode">
                                <em 
                                    v-if="file.IsDocCode == 1"
                                    :class="GetFileIconClass(file.DocExtention)" :alt='file.FileType' :title='file.Name'
                                    :data-fileType='file.DocExtention'
                                    @click="FilesDetail(file.Id, file.IsDocCode, file.DocPathLocation, file.DocExtention, file.ParentId, file.PERMISSION)"></em>
                            </RouterLink>
                            <em  v-if="file.IsDocCode == 0"
                                class='fa fa-folder pr-2 font-18 text-primary pt-3' :alt='file.FileType' :title='file.DocType'
                                @click="StorageDocumentListing(file.Id, commonParentName)"
                                :data-fileType='file.DocExtention'></em>
                        </div>

                        <div class='row mx-0 mb-3  align-items-end'>
                            <div class='col-md-12 font-14 text-center'>
                                <span class="text-overflow-dynamic-container text-truncate" :title="file.NAME"
                                    v-if="file.IsDocCode == 0">
                                    {{ file.pin_id }}

                                    <span style='display:block;  padding-bottom:5px;' :title='file.FileName'>
                                        {{ file.NAME }}
                                    </span>
                                </span>

                                <span class="text-overflow-dynamic-container text-truncate" :title="file.NAME"
                                    v-if="file.IsDocCode == 1">
                                    <span style='display:block; padding-bottom:5px;' :title='file.FileName'>
                                        {{ file.NAME }}
                                    </span>
                                </span>
                            </div>
                            <div class='col-md-12 font-14 text-center' v-if="file.IsDocCode == 1"><span>{{
                                convertmbtoKb(file.DocSize)
                            }} </span> |
                                <span>{{
                                    $options.filters.formatDate(file.created_at)
                                }}</span>
                            </div>
                            <div class='col-md-12 font-14 text-center' v-if="file.IsDocCode == 0">
                                <em aria-hidden="true"></em>{{ file.FileCount + file.folderCount }} Files |
                                <em aria-hidden="true"></em>{{ convertmbtoKb(file.DocSize) }}
                            </div>
                            <!-- <div class='col-md-6 text-right'>
                            </div> -->
                        </div>
                    </div>
                  
                </div>
               
            </div>
            <div v-if="listType == 'CardView' && listingdata.length==0" class="table-responsive table-fix-header text-center p-2 border" style="">
                <span class="text-center text-danger no-record">{{ $t("NoRecordfound") }}</span>
            </div>
            <div class="border-top-pagi">
                <Pager v-bind:numberofrecords="totalRecords" prev-text="Prev" 
                    :pageNo="PageNumber" first-text="First" last-text="Last" next-text="Next" v-on:SetCurrentPage="pagerMethod"></Pager>
            </div>

        </div>
    </div>
</template>

<script>
import Pager from "./Common/Pager.vue"
import DataService from "../services/DataService";
import fileUpload from "./Common/file-upload.vue";
export default {
    components: {
        Pager,
        fileUpload
    },
    data() {
        return {
            isLoading: false,
            hover: false,
            parentLength: null,
            loggedcompanyid: null,
            loggeduserid: null,
            PageNo: 1,
            listType: 'ListView',
            searchCondition: null,
            sortExp: null,
            specificSortClick: null,
            checkThis: this,
            listCode: null,
            token: null,
            pageSize: 10,
            docPermission: null,
            isShow: false,
            isShowUploadDiv: false,
            doccode: null,
            moveFolderObj: [],
            pinIdArray: [],
            userType: null
        }
    },
    props: {
        listingdata: {
            type: Array,
            required: true
        },
        sharedwith: {
            type: Array,
            required: true
        },
        user: {
            type: Number,
            required: true
        },
        // Grouplistingdata: {
        //     type: Array,
        //     required: true
        // },
        totalRecords: {
            type: Number,
            required: true
        },
        TotalPages: {
            type: Number,
            required: true
        },
        parentname: {
            type: Array,
            required: true
        },
        // sharedFilesListingData: {
        //     type: String,
        //     required: true
        // },
        isShowSharedListing: {
            type: Boolean,
            required: true
        },
        isShowMySpaceListing: {
            type: Boolean,
            required: true
        },
        folderPermission: {
            type: String,
            required: true
        },
        commonParentName: {
            type: String,
            required: true
        },
         folderId: null,
        showUploadDiv:
        {
            type: Boolean,
            required: true
        },
        PageNumber: {
            type: Number,
            required: false
        },
        routeClass: {
            type: String,
            required: false
        },
        refCode: {
            type: String,
            required: false
        },
        storePageNo: null,
        listingView: {
            type: String,
            required: false
        },
        folderSummary: null,
        // loggeduserid: null,
    },
    updated: function () {
        debugger
        this.BindActionButtonEvent();
        this.onChange();
        this.CheckBoxBootstrap();
       
    },
    created: function () {
        this.token = this.GetUserInfo.Token;
        this.userType = this.GetUserInfo.UserType;
        this.loggedcompanyid = this.GetUserInfo.CompanyId;
        this.loggeduserid = this.GetUserInfo.ID;
        this.dateformat = this.GetUserInfo.DateFormat;
        //this.GetSharedFiles();
        this.onChange();
    },


    methods: {
        openActionIcon(e) {
            e.target.nextSibling.removeAttribute('style');
            e.target.nextSibling.setAttribute('style', 'width:240px;');
            e.target.nextSibling.style.display = 'block';
        },
        ExpandCollapseList() {
            var element1 = document.getElementById("leftmenutree");
            element1.classList.toggle("left-menu-tab-collapsed");
            var element2 = document.getElementById("rightMenuTree");
            element2.classList.toggle("right-content-expand");
        },
        GetFileIcon: function(extnsn){
      var cls=null;
      if(extnsn=='mp3'){
        cls="fa fa-file-audio-o text-danger"
      }
      else if(extnsn=='mp4'){
         cls="fa fa-file-video-o text-info"
      }
      else if(extnsn=='docx'){
         cls="fa fa-file-word-o text-primary"
      }
      else if(extnsn=='pdf'){
         cls="fa fa-file-pdf-o"
      }
      else if(extnsn=='jpg' || extnsn=='png' || extnsn=='jpeg'){
         cls="fa fa-file-image-o"
      }
      else if(extnsn=="xls" || extnsn=="xlsx"){
          cls="fa fa-file-excel-o text-sucess"
      }
      else if(extnsn=="zip"){
          cls="fa fa-file-archive-o"
      }
      else if(extnsn=="ppt"){
          cls="fa fa-file-powerpoint-o text-primary"
      }
      else if(extnsn=="Programming"){
          cls="fa fa-file-code-o"
      }
      else if(extnsn=="Web"){
          cls="fa fa-globe text-primary"
      }
      else if(extnsn=="" || extnsn==null){
          cls="fa fa-folder text-primary"
      }
      else{
        cls="fa fa-file-o"
      }
      return cls;
    },
        closeActionIcon(e) {
            e.target.nextSibling.style.display = 'none';
        },
        updatelistafterupload(folderId) {
            this.$emit("StorageDocumentListing", folderId);
        },
        RefreshPage: function () {
            var vm = this;
            window.location.reload();
        },
        DownloadFiles: function (fileId, fileName) {

            var vm = this;
            var IsVersion = 0
            this.isLoading = true;
            vm.DownloadFile(fileId, fileName, IsVersion)
            this.isLoading = false;
        },
        DownloadFile: function (fileId, fileName,IsVersion) {
            var vm = this;
            var params = `fileId=${fileId}&IsVersion=${IsVersion}`
            DataService.DownloadFile(params).then((response) => {
                var fileURL = window.URL.createObjectURL(new Blob([response.data]));
                var fileLink = document.createElement('a');
                fileLink.href = fileURL;
                fileLink.setAttribute('download', fileName.toString());
                document.body.appendChild(fileLink);
                fileLink.click();

            })

        },
        pagerMethod: function (s) {
            debugger
             // eslint-disable-line no-debugger
             var vm=this;
            vm.$emit("PagerData", parseInt(s.PageNumber), vm.commonParentName, vm.specificSortClick, vm.sortExp, s.PageSize, vm.folderId, vm.searchCondition);
        },
        SearchData: function (name) {
           // debugger
            var vm = this;
            vm.$emit("PagerData", 1, vm.commonParentName, vm.specificSortClick, vm.sortExp, vm.pageSize, parseInt(vm.folderId), vm.searchCondition.trim())

        },
        GetThumbnail: function (path) {
            var cls = null;
            if (path != null) {
                var incStr = path.includes("wwwroot");
            }
            if (incStr == true) {
                cls = path.replace("/wwwroot", "");
            }
            else {
                cls = path;
            }
            return cls;
        },
        SpecificFolder: function (id, name) {
            //debugger
            var vm = this;
            var scondition = "";
            if (vm.searchCondition == null) {
                scondition = vm.searchCondition
            }
            else {
                scondition = vm.searchCondition.trim();
            }
            if (name == "My Space" || name == "Recently Added" || name == "Pinned" || name == "Shared") {
                /* StorageDocumentListing is a function name and rest are the parameter(folderiddata, Parentid, actionVal, name, searchcondition, sortBy, sortExp, listingCode)*/
                vm.$parent.StorageDocumentListing(id, '', '', name, scondition, '', '', vm.listCode, vm.docPermission, vm.storePageNo);
                this.$router.push({ path: "/File/DashBoard", query: { FolderId: id, ParentName: name, PageNo: vm.PageNo } });
            }
            else {
                vm.$parent.StorageDocumentListing(id, '', '', name, scondition, '', '', vm.listCode, vm.docPermission), vm.storePageNo;
                this.$router.push({ path: "/File/DashBoard", query: { FolderId: id, ParentName: name, RefCode: name, PageNo: vm.PageNo } });
                //this.$router.push({ path: "/File/DashBoard", query: { FolderId: folderId, ParentName: name, RefCode: 'Pinned', AddClass: aid } });
            }

        },
        GetFileIconClass: function (ext) {
            ext = ext.toLowerCase();
            var fileclass = "";
            if(this.listType=='ListView')
            {
                if (ext == 'png' || ext == "jpeg" || ext == "jpg" || ext == "JPG" || ext == "jfif" || ext == "pdf" || ext == "xlsx" || ext == "docx" || ext == "pptx") {
                    fileclass='d-none';
                }
                else if (ext == "txt") {
                    fileclass = "fa fa-file-text-o"
                }
                else if (ext == "xls") {
                    fileclass = "fa fa-file-excel-o"
                }
                else if (ext == "mp3") {
                    fileclass = "fa fa-file-audio-o text-danger"
                }
                else if (ext == "mp4") {
                    fileclass = "fa fa-file-video-o"
                }
                else if (ext == "zip") {
                    fileclass = "fa fa-file-archive-o"
                }
                else {
                    fileclass = "fa fa-file-o"
                }
                return fileclass
            }
            else{
                if (ext == 'png' || ext == "jpeg" || ext == "jpg" || ext == "JPG" || ext == "jfif" || ext == "pdf" || ext == "xlsx" || ext == "docx" || ext == "pptx") {
                    fileclass='d-none';
                }
                else if (ext == "txt") {
                    fileclass = "fa fa-file-text-o pt-3"
                }
                else if (ext == "xls") {
                    fileclass = "fa fa-file-excel-o pt-3"
                }
                else if (ext == "mp3") {
                    fileclass = "fa fa-file-audio-o pt-3"
                }
                else if (ext == "mp4") {
                    fileclass = "fa fa-file-video-o pt-3"
                }
                else {
                    fileclass = "fa fa-file-o pt-3"
                }
                return fileclass
            }
        },
