                vm.listCode = "Recent"
            }
            var clases = ev.target.getAttribute('class');
            vm.specificSortClick = sortBy;
            if (clases == 'sort' || clases == 'sort tb_headerSortUp') {
                vm.sortExp = 'ASC'
            }
            if (clases == 'sort tb_headerSortDown') {
                vm.sortExp = 'DESC'
            }
            // StorageDocumentListing is a function name and rest are the parameter(folderiddata, Parentid, actionVal, name, searchcondition, sortBy, sortExp, listingCode,sharedPermission)
            vm.$emit("StorageDocumentListing", vm.folderId, '', '', name, '', sortBy, vm.sortExp, vm.listCode, vm.docPermission)
