        //Emit method Dashboard.vue(Specific FolderListing)
        StorageDocumentListing: function (folderId, name, permission, aid) {
           // debugger
            var vm = this;
            vm.docPermission = permission;
            if (name == "My Space" || name == undefined) {
                vm.$emit("StorageDocumentListing", folderId, '', '', 'My Space', '', '', '', 'MySpace', vm.docPermission, 1);
                this.$router.push({ path: "/File/DashBoard", query: { FolderId: folderId, ParentName: name, RefCode: 'MySpace', AddClass: aid } });
            }
            else if (name == "Recently Added") {
                vm.$emit("StorageDocumentListing", folderId, '', '', 'Recently Added', '', '', '', 'Recent', vm.docPermission, vm.PageNo);
                this.$router.push({ path: "/File/DashBoard", query: { FolderId: folderId, ParentName: name, RefCode: 'Recent', AddClass: aid } });
            }
            else if (name == "Pinned Folders") {
                vm.$emit("StorageDocumentListing", folderId, '', '', 'Pinned Folders', '', '', '', 'Pinned', vm.docPermission, vm.PageNo);
                this.$router.push({ path: "/File/DashBoard", query: { FolderId: folderId, ParentName: name, RefCode: 'Pinned', AddClass: aid } });
            }
            else if (name == "Shared Folders" || name == 'Shared') {
                vm.$emit("StorageDocumentListing", folderId, '', '', 'Shared Folders', '', '', '', 'Shared', vm.docPermission, vm.PageNo);
                this.$router.push({ path: "/File/DashBoard", query: { FolderId: folderId, ParentName: name, RefCode: 'Shared', AddClass: aid } });
            }

