        convertmbtoKb: function (val) {
            var convertKb = 0;
            if (val == 0) {
                convertKb = '0 Bytes'
            }
            else if(val.toFixed(3)=="0.000")
            {
                var mbs=val* (1024 * 1024);
                convertKb = mbs.toFixed(2) + ' Bytes'
            }
             else
                if (val < 1) {
                    convertKb = val.toFixed(2) + ' KB'
                }
                else {
                    convertKb = val.toFixed(2) + ' MB'
                }
            return convertKb;
        },
