<!-- Assign Tag popup template -->
<template v-for="item in GroupsData">
<div>
    <div class="my-popups">
        <div class="modal d-block ">
            <div class="modal-dialog modal-dialog-centered ui-draggable">
                <div class="modal-content">
                    <div class="modal-header ui-draggable-handle">
                        <h4 name="header" class="modal-title">
                            {{$t('AssingTag')}}
                        </h4>
                        <button @click="ClosePopupTag" class="close"><em aria-hidden="true"
                                class="fa fa-times"></em> </button>
                    </div>
                    <div class="modal-body">
                        <Form v-slot="{ values, meta }" ref="form">
                            <section id="divMainContent" class="main-content">
                                <div class="container-fluid">                                    
                                        <div class="row">
                                            <div class="col-lg-12 p-0">
                                               
                                                    <div class="form-group">
                                                        <label style="font-size:18px;">Tags: <span class="text-danger"  v-if="IsRequiredDiv">*</span></label>
                                                    <div class="row">
                                                        <div class="col-12 float-left">
                                                          <div class="input-group">
                                                            <Multiselect v-model="SelectedValue" :limit="1" :limitText="count => `and ${count} more`" :multiple="true" :options="Options" style="padding-left:0px;" class="m-0 form-control border-0"
                                                            mode="tags" v-bind="field" :close-on-select="false" track-by="value" label="label"
                                                            :searchable="true" :taggable="false" placeholder="Select Tags">
                                                            </Multiselect>
                                                          <div class="ml-2 mt-2" v-if="userType=='CA'">
                                                            <a class="round-icon-small btn-dark theme-primary" @click="OpenAddTagPopup">
                                                                <em class="fa fa-plus text-white pt-7 font-18"></em>
                                                            </a>
                                                        </div></div>
                                                        </div>
                                                    </div>

                                                    </div>
                                               
                                                <!-- <div class="custom-control custom-radio custom-control-inline pl-0">
                                                <a title="Add" class="round-icon-small btn-dark theme-primary float-right"
                                                    ><em class="fa fa-plus text-white pt-7 font-18"></em> </a>
                                            </div> -->
                                               

                                            </div>
                                        </div>                                   
                                </div>
                            </section>
                        </Form>
                    </div>
                    <div class="modal-footer text-right">
                        <button title="Search" class="popupgreenbtn btn btn-success formbtn widthhalf"
                            v-on:click="SaveAssignTag"><em class="fa fa-floppy-o pr-2"></em>  {{$t('Save')}}</button>
                        <button title="Cancel" @click="ClosePopupTag"
                            class="popupredbtn btn btn-danger formbtn widthhalf pr-2"><em aria-hidden="true"
                                class="fa fa-times pr-2"></em>  {{$t('Cancel')}}</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <ModalAddTagPopup @closeAddTagPopup="closeAddTagPopup" :isDivActiveFromTagListing="isDivActiveFromTagListing" :isDivActive="isDivActive" v-if="showAddTagPopup">
    </ModalAddTagPopup>
</div>
  
</template>
<script>
import DataService from "../../../services/DataService";
import ModalAddTagPopup from "../../../views/UI/Modal/ModalSaveTagPopup.vue";
export default {
    components:
    {
        ModalAddTagPopup,
    },
    props:{
        folderId:{
            type: String,
        },
        selectedtags:null
    },
    data() {
        return {
            tagdata: [],
            tagValue: null,
            myobj: [],
            doccode: "",
            errorMessage: "",
            validate: null,
            SelectedValue: [],
            Options: [], 
            name: 'A',
            selected: [],
            options: ['list_a', 'of_a', 'options_a'],
            showAddTagPopup:false,
            isDivActiveFromTagListing:false,
            isDivActive:false,
            userType:null
        }
    },
    created: function () {
        this.userType=this.GetUserInfo.UserType;
        this.AssignTag();
    },
    updated: function(){
        this.AssignTag();
    },
    methods: {
        /*
         OpenAdd Tag popup
        */
        OpenAddTagPopup: function ()
        {
            
            this.isDivActiveFromTagListing=false;
            this.isDivActive=true;
            this.showAddTagPopup=true;
        },
        limitTextFun(count){
        return `and ${count} more`;
        },
        closeAddTagPopup: function()
        {
            this.showAddTagPopup=false;
        },
        onChange(e) {
            
            var vm = this;
            vm.tagValue = e.target.value;
            vm.validateForm(vm.tagValue);
        },
        AssignTag: function () {
            var vm = this;
            var val='';
            vm.Options=[];
            let params = {};
            params["type"] = "DOC_TAG";
            var param="moduleName=DocStorage&type=DOC_TAG"
            DataService.GetDDLDatas(param).then((response) => {                
                vm.tagdata = response.data.DATA;
                vm.tagdata.forEach((item, index) => {
                            vm.Value = item.value;
                            vm.first_value = item.name
                            vm.Options.push({
                                value: vm.Value,
                                label: vm.first_value
                            })
                            debugger;
                            if(vm.selectedtags){
                               var selectedTagIds= vm.selectedtags.split(',');
                               selectedTagIds.forEach((tagid, index) => {
                                   if (vm.Value == tagid) {
                                       vm.SelectedValue.push({
                                           value: tagid,
                                           label: vm.first_value
                                       })

                                   }
                               })
                            }                            
                        });
                        console.log(vm.SelectedValue)
            })
        },
        SaveAssignTag: function () {
            debugger;
            var vm = this;
            vm.isLoading=true;
            var val = "";
            vm.myobj=[]
            if(vm.SelectedValue.length==0){
                $('#tblFileList .chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                    vm.doccode = $(this).attr("doccode")
                    val = $(this).val();
                    let obj={};
                        obj = {
                        TagId: '',
                        SourceId: val,
                        RefCode: vm.doccode
                    }
                    vm.myobj.push(obj);
                    var params = JSON.stringify(vm.myobj);
                    DataService.ManageTalygenTags(params).then((response) => {
                        
                        if (response.data == 1) {
                            $('.chkItems:checkbox:checked,#chkAll_0').prop("checked", false).removeAttr('checked');
                            vm.ShowAlert(vm.$t("TagRemoved"), "success", true, vm.$t("Alert"));
                            vm.$parent.ClosePopupTag();
                            vm.$emit("StorageDocumentListing",vm.folderId);
                        }
                        else {
                            vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                        }
                        vm.isLoading=false;
                    });
                });
            }
            else{
                vm.validateForm(vm.SelectedValue);
                if (vm.validate == true) {
                    $('#tblFileList .chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                        vm.doccode = $(this).attr("doccode")
                        val = $(this).val();
                        let obj={};
                        vm.SelectedValue.forEach((item, index) => {
                            obj = {
                            TagId: item.value,
                            SourceId: val,
                            RefCode: vm.doccode
                        }
                        vm.myobj.push(obj);
                        })
                    });

                    var params = JSON.stringify(vm.myobj);
                    DataService.ManageTalygenTags(params).then((response) => {
                        
                        if (response.data == 1) {
                            $('.chkItems:checkbox:checked,#chkAll_0').prop("checked", false).removeAttr('checked');
                            vm.ShowAlert(vm.$t("AssignTag"), "success", true, vm.$t("Alert"));
                            vm.$parent.ClosePopupTag();
                            vm.$emit("StorageDocumentListing",vm.folderId);
                        }
                        else {
                            vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                        }
                        
                    vm.isLoading=false;
                    });
                }
            }

        },
        ClosePopupTag: function () {
            this.$parent.ClosePopupTag();
        },
        validateForm(id) {
            
            var vm = this;
            if (id == "" || id == null) {
                vm.errorMessage = vm.$t("TagIsRequired");
                // vm.$("#assignTag").addClass("is-invalid");
                vm.validate = false;
            }
            else {
                vm.errorMessage = "";
                vm.validate = true;
            }
        }
    }
}
</script>
<style>
.my-popups .modal-dialog {
    max-width: 902px;
}
</style>